import GameGlobal from "../Global";
import TunnelSprite from "../logic/TunnelSprite";
import MouseMeshScript from "./device/MouseMeshScript";
import GameUI from "./GameUI";

export default class CameraScript extends Laya.Script3D{
    /**
     * 固定朝向
     */
    public static UP_V3:Laya.Vector3 = new Laya.Vector3(0, 1, 0);
    public static UP_V3_2:Laya.Vector3 = new Laya.Vector3(-1, 0, 0);

    protected  camera:Laya.BaseCamera;
    protected  scene:Laya.Scene3D;

    /**
     * 摄像机初始坐标
     */
    private _initCamerPos:Laya.Vector3 = new Laya.Vector3();

    /**
     * 摄像机初始位置
     */
    private _initCamerDir:Laya.Quaternion = new Laya.Quaternion();

    /**
     * 摄像机默认是否在移动中
     */
    protected _moveing:boolean = false;
    /**
     * 当前查看点
     */
    public curLookPos:Laya.Vector3 = new Laya.Vector3();

    /**
     * 自动巡检id
     */
    public autoViewIndex:number = 0;
    /**
     * 是否当前自动巡游
     */
    public autoStat:boolean = false;
    /**
     * 上次暂停的巡游点
     */
    public stopBackView:Laya.Vector3;

    /**
     * 临时目标点
     */
    private _tempMoveTarget:Laya.Vector3 = new Laya.Vector3();

    /**
     * 点击设备时备份
     */
    private _clickBackPos:Laya.Vector3 = new Laya.Vector3();
    private _clickBackLook:Laya.Vector3 = new Laya.Vector3();

    private _maxViewZ:number = 35;

    public _minViewZ:number = 0;
    constructor(){
        super();
    }

    /**
     * 虚函数
     * @param bResetLook 
     */
    initTransform(bResetLook:boolean= false){

    }
        
    /**
     * 摄像机还原到默认位置
     */
    public reset(){
        GameGlobal.tunnelMgr.setSelectDevice(null);
        this.curLookPos.setValue(0, 0, 0);
        this.tweenMoveTo(this._initCamerPos, 500, this._initCamerDir);
    }

    /**
     * 点击设备时备份
     */
    public clickViewBack(){
        this.camera.transform.position.cloneTo(this._clickBackPos);
        this.curLookPos.cloneTo(this._clickBackLook);
    }
    
    /**
     * 点击设备时备份
     */
    public clickViewReset(){
        this._clickBackLook.cloneTo(this.curLookPos);
        this.tweenMoveTo(this._clickBackPos, 100, null, this.curLookPos);
    }

    /**
     * @inheritDoc
     */
    public  onAwake():void {
        this.camera = this.owner as Laya.Camera;
    }
    public onStart(){
        this.camera.transform.position.cloneTo(this._initCamerPos);
        this.camera.transform.rotation.cloneTo(this._initCamerDir);
    }
    /**
     * 缓动移动到指定点和方向
     * @param pos
     * @param dir
     * @param nTime
     */
    public tweenMoveTo(pos:Laya.Vector3, nTime:number, dir:Laya.Quaternion = null, lookPos:Laya.Vector3=null, endCall:Function = null, bup:boolean = false){
        if(!endCall){
            this.stopView();
        }
        let curpos = this.camera.transform.position;
        this._moveing = true;
        Laya.Tween.clearAll(curpos);
        Laya.Tween.to(curpos, {x:pos.x,y:pos.y,z:pos.z,
            update:new Laya.Handler(this, function(){
                this.camera.transform.position = curpos;
                if(lookPos){
                    this.camera.transform.lookAt(lookPos, bup ? CameraScript.UP_V3_2 : CameraScript.UP_V3, false);
                }
            })
        }, nTime, null, Laya.Handler.create(this, function(){
            this.moveOver();
            if(endCall){
                endCall();
            }
        } ));
        if(!lookPos && dir){ // 固定查看点
            let curdir = this.camera.transform.rotation;
            Laya.Tween.clearAll(curdir);
            Laya.Tween.to(curdir, {x:dir.x,y:dir.y,z:dir.z,w:dir.w,
                update:new Laya.Handler(this, function(){
                    this.camera.transform.rotation = curdir
                })
            }, nTime);
        }
    }

    /**
     * 移动结束
     */
    public moveOver(){
        Laya.Tween.clearAll(this.camera.transform.position);
        this._moveing = false;
    }

    onUpdate(){
        if(GameUI.instance){
            GameUI.instance.setShowPos(-this.camera.transform.position.z);
        }
    }

    /**
     * 停止巡游
     * @returns
     */
    public stopView(){
        if(!this.autoStat){
            return;
        }
        this.autoStat = false;
        if(!this.stopBackView){
            this.stopBackView = new Laya.Vector3();
        }
        this.camera.transform.position.cloneTo(this.stopBackView);
        this.moveOver();
        GameUI.instance.updateViewShow();
        let msg = {
            msgType:"autoViewEvt",
            msgData:this.autoStat,
        }
        Laya.Browser.window.parent.postMessage(msg);
    }

    /**
     * 自动巡游
     */
    public autoView(bAuto:boolean){
        if(!bAuto){
            this.stopView();
            return;
        }

        let obj = GameGlobal.tunnelMgr.getCurView(this.autoViewIndex);
        if(!obj){
            return false;
        }
        GameGlobal.tunnelMgr.setSelectDevice(null);
        let viewPos = obj.transform.position;
        if(!this.autoStat){ // 如果是开始巡游 则通知
            this.autoStat = bAuto;
            let msg = {
                msgType:"autoViewEvt",
                msgData:this.autoStat,
            }
            Laya.Browser.window.parent.postMessage(msg);
            GameUI.instance.updateViewShow();
        }
        
        if(this.stopBackView){
            this.camera.transform.position = this.stopBackView;
            this.stopBackView = null;
        }
        let dis = 3;
        let h = 2;
        let pos = MouseMeshScript.TEMP_CAMERA_POS
		pos.x = 0;
		pos.y = 0;
		pos.z = dis;

		pos.y += h;
		Laya.Vector3.add(viewPos, pos, pos);

		viewPos.cloneTo(this.curLookPos);
        this.curLookPos.y += h;

        let moveDis = Laya.Vector3.distance(pos, GameGlobal.GameCamera.transform.position);
        let nTime = moveDis * 200;
        if(this.autoViewIndex %2 == 0){
            nTime = 400;
        }
		this.tweenMoveTo(pos, nTime, null, GameGlobal.CameraScript.curLookPos, function(){
            GameGlobal.CameraScript.autoViewIndex ++;
            if(GameGlobal.CameraScript.autoViewIndex > 3){
                GameGlobal.CameraScript.autoViewIndex = 0;
            }
            GameGlobal.CameraScript.autoView(true);
        });
    }

    /**
     * 上帝视角
     */
    moveToTopView(){
        GameGlobal.tunnelMgr.setSelectDevice(null);
        let pos = this._tempMoveTarget;
        this.camera.transform.position.cloneTo(pos);
        if(pos.z > this._maxViewZ){
            pos.z = this._maxViewZ;
        }else if(pos.z < this._minViewZ){
            pos.z = this._minViewZ;
        }
        pos.setValue(-15, 85, pos.z);
        this.curLookPos.setValue(-15, 0, pos.z);
        this.tweenMoveTo(pos, 300, null, this.curLookPos, null, true);
    }

    /**
     * 右洞外
     */
    moveToRightHole(){
        GameGlobal.tunnelMgr.setSelectDevice(null);
        let pos = this._tempMoveTarget;
        this.camera.transform.position.cloneTo(pos);
        if(pos.z > this._maxViewZ){
            pos.z = this._maxViewZ;
        }else if(pos.z < this._minViewZ){
            pos.z = this._minViewZ;
        }
        pos.setValue(36, 48, pos.z);
        this.curLookPos.setValue(-6, 0, pos.z);
        this.tweenMoveTo(pos, 300, null, this.curLookPos);
    }

    /**
     * 左洞外
     */
    moveToLeftHole(){
        GameGlobal.tunnelMgr.setSelectDevice(null);
        let pos = this._tempMoveTarget;
        this.camera.transform.position.cloneTo(pos);
        if(pos.z > this._maxViewZ){
            pos.z = this._maxViewZ;
        }else if(pos.z < this._minViewZ){
            pos.z = this._minViewZ;
        }
        pos.setValue(-68, 47, pos.z);
        this.curLookPos.setValue(-20, 0, pos.z);
        this.tweenMoveTo(pos, 300, null, this.curLookPos);
    }

    /**
     * 右洞内
     */
    moveToRightIn(){
        GameGlobal.tunnelMgr.setSelectDevice(null);
        let pos = this._tempMoveTarget;
        this.camera.transform.position.cloneTo(pos);
        if(pos.z > this._maxViewZ){
            pos.z = this._maxViewZ;
        }else if(pos.z < this._minViewZ){
            pos.z = this._minViewZ;
        }
        pos.setValue(0, 2.5, pos.z);
        this.curLookPos.setValue(0, 2.5, pos.z-5);
        this.tweenMoveTo(pos, 300, null, this.curLookPos);
    }
    /**
     * 左洞内
     */
     moveToLeftIn(){
        GameGlobal.tunnelMgr.setSelectDevice(null);
        let pos = this._tempMoveTarget;
        this.camera.transform.position.cloneTo(pos);
        if(pos.z > this._maxViewZ){
            pos.z = this._maxViewZ;
        }else if(pos.z < this._minViewZ){
            pos.z = this._minViewZ;
        }
        pos.setValue(-TunnelSprite.POS_OFFSET_VALUE, 2.5, pos.z);
        this.curLookPos.setValue(-TunnelSprite.POS_OFFSET_VALUE, 2.5, pos.z-5);
        this.tweenMoveTo(pos, 300, null, this.curLookPos);
    }

    /**
     * z轴步进
     * @param x 
     */
    moveZ(x:number, ntime:number = 200){
        let pos = this._tempMoveTarget;
        this.camera.transform.position.cloneTo(pos);
        pos.z += x;
        this.curLookPos.z += x;
        this.tweenMoveTo(pos, ntime);
    }
}