import MouseMeshScript from "./MouseMeshScript";

/***
 * 风机
 */
export default class DraughtScript extends MouseMeshScript {

    public static ROTATE_TYPE_VALUE_1:Laya.Vector3 = new Laya.Vector3(0, 0.1, 0);
    public static ROTATE_TYPE_VALUE_2:Laya.Vector3 = new Laya.Vector3(0, -0.1, 0);
    private _tempColor = new Laya.Vector4(1, 0, 0, 1);
    /**
     * 旋转类型0不旋转，1顺时针，2反时针
     */
    private _rotateType:number = 0;

    private _fengshanMesh:Laya.MeshSprite3D;
    constructor(){
        super();
    }


    onStart(){
        super.onStart();
        this._camerOffsetHeight = 0.5;

        this._fengshanMesh = this.owner.getChildByName("sd_wj_sd_fengji01_02") as Laya.MeshSprite3D;

        // 设置在场景中高度
		this.nodeSprite.transform.localPosition.y = 6.5;
        //this.nodeSprite.transform.localPosition.z = 0;
		this.nodeSprite.transform.localPosition = this.nodeSprite.transform.localPosition;

        this._rotateType = 2;
        this.updateState();
    }

    onUpdate(){
        super.onUpdate();
        if(!this._fengshanMesh){
            return;
        }
        switch(this._rotateType){
            case 1:
                this._fengshanMesh.transform.rotate(DraughtScript.ROTATE_TYPE_VALUE_1);
                break;
            case 2:
                this._fengshanMesh.transform.rotate(DraughtScript.ROTATE_TYPE_VALUE_2);
                break;
        }
    }

    updateState(){
        this._rotateType = this.nodeSprite.DeviceData.workMode;
    }

    changeData(data:any){
        super.changeData(data);
        this.updateState();
    }
}