import EnumType from "../../logic/EnumType";
import ScnenDevice from "../../logic/ScnenDevice";
import MouseMeshScript from "./MouseMeshScript";

export default class Light01Script extends MouseMeshScript{

    constructor(){
        super();
    }

    onAwake(){
        this.nodeSprite = this.owner.parent as ScnenDevice;
        if(this.nodeSprite.deviceType == EnumType.DEVICE_TYPE_LIGHT_ROAD){
            this._defaultX = 10000;
        }else{
            this._defaultX = 4;
            if(this.nodeSprite.DeviceData.orientationLocation == 17){
                this._defaultX = 14;
            }
        }

        super.onAwake();
    }

    onStart(){
        super.onStart();

        this._camerOffsetDis = 5;
        this._camerOffsetHeight = 0;

        if(this.nodeSprite.deviceType == EnumType.DEVICE_TYPE_LIGHT_ROAD){
            this._camerOffsetDis = 12;
            this._camerOffsetHeight = 12;
            this.nodeSprite.transform.localPosition.y = 5.253753; // 固定高度偏移
            this.nodeSprite.transform.localPosition.x = 0.73; // 固定高度偏移
        }else{
            this.nodeSprite.transform.localPosition.y = 7; // 固定高度偏移
            if(this.nodeSprite.DeviceData.orientationLocation != 17){
                this.nodeSprite.transform.rotate(new Laya.Vector3(0, -90, 0), true, false);
            }else{// 横洞中的距离单独处理
                this.nodeSprite.transform.localPosition.y = 3; // 固定高度偏移
                let ms = this.owner as Laya.Sprite3D;
                ms.transform.localPosition.z = -4;
                ms.transform.localPosition = ms.transform.localPosition;
            }
        }

        this.nodeSprite.transform.localPosition = this.nodeSprite.transform.localPosition;
        this.changeData();
    }

    setOpen(bOpen:boolean){
        let sp3 = this.owner as Laya.Sprite3D;
        let light = sp3.getChildByName("sd_dadeng02") as Laya.MeshSprite3D;
        if(light){
            light.getChildAt(0).active = bOpen;
            let mat = light.meshRenderer.material as Laya.EffectMaterial;
            if(bOpen){
                mat.colorA = 1;
            }else{
                mat.colorA = 0.1;
            }
        }

        let light2 = sp3.getChildByName("Light_01");
        if(light2){
            sp3.getChildAt(0).active = bOpen;
            sp3.getChildAt(1).active = bOpen;
        }
    }

    changeData(){
        this.setOpen(this.nodeSprite.DeviceData.workMode == 1);
    }
}