import EnumType from "../../logic/EnumType";
import ScnenDevice from "../../logic/ScnenDevice";
import MouseMeshScript from "./MouseMeshScript";

export default class AlertorScript extends MouseMeshScript{
    private _lastShowTime:number = 0;
    constructor(){
        super();
    }

    onAwake(){
        this._camerOffsetDis = 5;
        this._camerOffsetHeight = 0;
        this.nodeSprite = this.owner.parent as ScnenDevice;
        switch(this.nodeSprite.deviceType){
            case EnumType.DEVICE_TYPE_ALERTOR: // 火灾报警器
                this._camerOffsetDis = 8;
                this._defaultX = 7.4;
                break;
            case EnumType.DEVICE_TYPE_PHONE: // 电话
                this._defaultX = 7.2;
                break;
            case EnumType.DEVICE_TYPE_BROADCAST: // 广播
                this._defaultX = 6.2;
                break;
            case EnumType.DEVICE_TYPE_CAM: // 摄像机 
                this._defaultX = 6.17;
                if(this.nodeSprite.DeviceData.orientationLocation == 17){
                    this._defaultX = 10;
                }
                break;
        }
        super.onAwake();
    }

    onStart(){
        let y = 0;
        switch(this.nodeSprite.deviceType){
            case EnumType.DEVICE_TYPE_ALERTOR: // 火灾报警器
                y = 2.2;
                break;
            case EnumType.DEVICE_TYPE_PHONE: // 电话
                y = 1.45;
                break;
            case EnumType.DEVICE_TYPE_BROADCAST: // 广播
                y = 3.4;
                break;
            case EnumType.DEVICE_TYPE_CAM: // 摄像机 
                y = 4.96;
                if(this.nodeSprite.DeviceData.orientationLocation == 17){
                    y = 3;
                    let ms = this.owner as Laya.Sprite3D;
                    ms.transform.localPosition.z = -4;
                    ms.transform.localPosition = ms.transform.localPosition;
                }
                break;
        }
        this.nodeSprite.transform.localPosition.y = y; // 固定高度偏移
        //this.nodeSprite.transform.localPosition.x = 7;

        this.nodeSprite.transform.localPosition = this.nodeSprite.transform.localPosition;
        if(this.nodeSprite.DeviceData.orientationLocation != 17){
            this.nodeSprite.transform.rotate(new Laya.Vector3(0, -90, 0), true, false);
        }

        this.changeData();
    }

    onUpdate(){
        super.onUpdate()
        if(this.nodeSprite.deviceType != EnumType.DEVICE_TYPE_ALERTOR){// 火灾报警器
            return;
        }
        if(this.nodeSprite.DeviceData.workMode != 1){
            return;
        }
        let curtime =  Date.now();
        if(curtime - this._lastShowTime < 400){
            return
        }
        this._lastShowTime = curtime;
        let mesh = this.owner.getChildByName("sd_huozhaijingbao01_jingbaodeng");
        if(mesh){
            mesh.active = !mesh.active;
        }
    }

    changeAlertor(){
        let mesh = this.owner.getChildByName("sd_huozhaijingbao01_jingbaodeng");
        if(mesh){
            mesh.active = this.nodeSprite.DeviceData.workMode == 1;
        }
        let mesh1 = this.owner.getChildByName("Light");
        if(mesh1){
            mesh1.active = this.nodeSprite.DeviceData.workMode == 1;
        }
    }
    changePhone(){
        let mesh = this.owner.getChildByName("Light");
        if(mesh){
            mesh.active = this.nodeSprite.DeviceData.workMode == 1;
        }
        let mesh1 = this.owner.getChildByName("lvdeng");
        if(mesh1){
            mesh1.active = this.nodeSprite.DeviceData.workMode == 1;
        }
    }

    changeData(){
        switch(this.nodeSprite.deviceType){
            case EnumType.DEVICE_TYPE_ALERTOR: // 火灾报警器
                this.changeAlertor();
                break;
            case EnumType.DEVICE_TYPE_PHONE: // 电话
                this.changePhone();
                break;
            case EnumType.DEVICE_TYPE_BROADCAST: // 广播
                let mesh1 = this.owner.getChildByName("pfx");
                if(mesh1){
                    mesh1.active = this.nodeSprite.DeviceData.workMode == 1;
                }
                break;
        }



    }
}