import GameGlobal from "../Global";
import RoadNode from "../logic/RoadNode";
import TunnelSprite from "../logic/TunnelSprite";
import { ui } from "./../ui/layaMaxUI";
/**
 * 本示例采用非脚本的方式实现，而使用继承页面基类，实现页面逻辑。在IDE里面设置场景的Runtime属性即可和场景进行关联
 * 相比脚本方式，继承式页面类，可以直接使用页面定义的属性（通过IDE内var属性定义），比如this.tipLbll，this.scoreLbl，具有代码提示效果
 * 建议：如果是页面级的逻辑，需要频繁访问页面内多个元素，使用继承式写法，如果是独立小模块，功能单一，建议用脚本方式实现，比如子弹脚本。
 */
export default class GameUI extends ui.test.TestSceneUI {
    /**设置单例的引用方式，方便其他类引用 */
    static instance: GameUI;

    //“upColor,overColor,downColor,disableColor”
    private _defBtnClolor:string = "#5391F3,#C1D9FF,#C1D9FF,#5391F3";
    constructor() {
        super();
        GameUI.instance = this;
		this.btnReset.on(Laya.Event.CLICK, this, this.onClickReset);
        this.btnAutoView.on(Laya.Event.CLICK, this, this.onClickAutoView)

        // this.siderMainLenth.changeHandler = new Laya.Handler(this, this.onChangeLenth);
        // this.siderMainLenth.max = GameGlobal.tunnelMgr.getMaxLenth();
        // this.siderMainLenth.visible = false;

        
        this.btnTopView.labelColors = this._defBtnClolor;
        this.btnRightHole.labelColors = this._defBtnClolor;
        this.btnLeftHole.labelColors = this._defBtnClolor;
        this.btnRightIn.labelColors = this._defBtnClolor;
        this.btnLeftIn.labelColors = this._defBtnClolor;
        this.btnReset.labelColors = this._defBtnClolor;
        this.btnAutoView.labelColors = this._defBtnClolor;
        this.btnEasy.labelColors = this._defBtnClolor;

        this.btnTopView.on(Laya.Event.CLICK, this, function(){
            GameGlobal.CameraScript.moveToTopView();
        });
        this.btnRightHole.on(Laya.Event.CLICK, this, function(){
            GameGlobal.CameraScript.moveToRightHole();
        });
        this.btnLeftHole.on(Laya.Event.CLICK, this, function(){
            GameGlobal.CameraScript.moveToLeftHole();
        });

        this.btnRightIn.on(Laya.Event.CLICK, this, function(){
            GameGlobal.CameraScript.moveToRightIn();
        });
        this.btnLeftIn.on(Laya.Event.CLICK, this, function(){
            GameGlobal.CameraScript.moveToLeftIn();
        });
        
        this.btnMoveLeft.on(Laya.Event.MOUSE_DOWN, this, function(){
            //GameGlobal.CameraScript.moveZ(5);
            GameGlobal.CameraScript.btnMoveValue = 3;
        });
        this.btnMoveRight.on(Laya.Event.MOUSE_DOWN, this, function(){
            //GameGlobal.CameraScript.moveZ(-5);
            GameGlobal.CameraScript.btnMoveValue = -3;
        });
        this.btnMoveLeft.on(Laya.Event.MOUSE_UP, this, function(){
            //GameGlobal.CameraScript.moveZ(5);
            GameGlobal.CameraScript.btnMoveValue = 0;
        });
        this.btnMoveRight.on(Laya.Event.MOUSE_UP, this, function(){
            //GameGlobal.CameraScript.moveZ(-5);
            GameGlobal.CameraScript.btnMoveValue = 0;
        });

        this.btnEasy.on(Laya.Event.CLICK, this, function(){
            GameGlobal.CameraScript.easyMode = !GameGlobal.CameraScript.easyMode;
            this.btnEasy.label = GameGlobal.CameraScript.easyMode ? "锁定模式" : "自由模式";
        });

        let startpos = GameGlobal.tunnelMgr.getMinStartPos();
        let maxlen = GameGlobal.tunnelMgr.getMaxLenth()
        let num = Math.ceil(maxlen / 1000) + 1;
        if(num == 1 || GameGlobal.tunnelMgr.showMode != 1){
            this.listPos.repeatX = 0;
            this.listPos.visible = false;
        }else{
            this.listPos.repeatX = num ;
            let ary = [];
            for(let i=0; i < num; ++i){
                let z = 1000 * i - 20;
                if(z > maxlen){
                    z = maxlen;
                }
                let pos = startpos + z;
                z *= GameGlobal.SHOW_SCALE;
                let z2 = z + 1000 * GameGlobal.SHOW_SCALE;
                if(i == 0){
                    ary.push([z, "K" + Math.floor(pos/1000) + "(起点)", z2]);
                }else if(i == num - 1){
                    ary.push([z, "K" + Math.floor(pos/1000) + "(终点)", z2]);
                }else{
                    ary.push([z, "K" + Math.floor(pos/1000), z2]);
                }
                
            }
            this.setPosList(ary);
        }

        this.resizeEvt();
        Laya.stage.on(Laya.Event.RESIZE, this, this.resizeEvt);
        this.imgShowInfo.visible = GameGlobal.SHOW_DEBUG;
    }

    public resizeEvt(){
        this.width = Laya.Browser.width;
        this.height = Laya.Browser.height;
    }

    public setMoveBtnState(b:boolean){
        this.btnMoveLeft.disabled = this.btnMoveRight.disabled = !b;
    }

    /**
     * 设置桩位分段列表
     */
    public setPosList(posList:Array<any>){
        this.listPos.array = posList;
        this.listPos.renderHandler = new Laya.Handler(this, function(box:Laya.Box, index:number){
            let btn = box.getChildAt(0) as Laya.Button;
            btn.labelColors = this._defBtnClolor;
            btn.label = this.listPos.array[index][1];
        });
        this.listPos.mouseHandler = new Laya.Handler(this, function(evt:Laya.Event, index:number){
            if(Laya.Event.CLICK != evt.type){
                return;
            }
            let info = this.listPos.array[index];

            GameGlobal.CameraScript.moveZ(- info[0] - GameGlobal.CameraScript.transform.position.z, 500);
        })
    }


    /**
     * 点击还原
     */
    private onClickReset(){
        GameGlobal.CameraScript.reset();
    }

    /**
     * 自动巡游
     */
    private onClickAutoView(){
        GameGlobal.CameraScript.autoView(!GameGlobal.CameraScript.autoStat);
    }

    public updateViewShow(){
        if(!GameGlobal.CameraScript.autoStat){
            if(GameGlobal.CameraScript.stopBackView){
                this.btnAutoView.label = "继续巡游";
            }else{
                this.btnAutoView.label = "自动巡游";
            }
            
        }else{
            this.btnAutoView.label = "停止巡游";
        }
    }

    /**
     * 隧道长度修改
     */
    private onChangeLenth(value:number){
        //let suidao01 = GameGlobal.GameScene.getChildByName("TunnelSprite1") as TunnelSprite;
        //suidao01.transform.position.y = value / 10;
        //suidao01.transform.position = suidao01.transform.position;
    }

    public setShowPos(num:number){
        if(!this.listPos.visible){
            return;
        }
        for(let i = 0; i < this.listPos.cells.length; ++i){
            let data = this.listPos.array[i]
            let box = this.listPos.cells[i];
            let btn = box.getChildAt(0) as Laya.Button;
            num = Math.ceil(num);
            if(num >= data[0] && num < data[2]){
                btn.selected = true;
            }else{
                btn.selected = false;
            }
            
        } 
    }
}