
import MouseMeshScript from "./MouseMeshScript"

/**
 * 信号灯
 */
export default class SignalLampScript extends MouseMeshScript{

    private _imgs:Array<string> = new Array("sd_jiaotongtubiao03", "sd_jiaotongtubiao01", "sd_jiaotongtubiao02", "sd_zuozhuandeng001"); // 交通信号灯：0绿灯 1红灯 2黄灯 3左转 4右转
    constructor(){
        super();
    }

    onAwake(){
        this._defaultX = 10000;
        super.onAwake();
    }

    onStart(){
        super.onStart();

		this._camerOffsetHeight = 15;
		this._camerLookHeight = 6;
        
        this.nodeSprite.transform.localPosition.x = -7.2;

        let bExit = this.nodeSprite.tunnelSprite.posType == 1;
        if(bExit){
            this.nodeSprite.transform.localPosition.x = 8.5;
            this.nodeSprite.transform.rotate(new Laya.Vector3(0, 180, 0), true, false);
        }
        this.nodeSprite.transform.localPosition = this.nodeSprite.transform.localPosition;
        this.changeData();
    }


    changeData(){
        let sp = this.owner as Laya.Sprite3D;
        for(let i=0;i < this._imgs.length; ++i){
            let img = sp.getChildByName(this._imgs[i]);
            img.active = (this.nodeSprite.DeviceData.workMode == i)
        }
    }
}