import MouseMeshScript from "./MouseMeshScript";

export default class JiFang1Script extends MouseMeshScript{
    constructor(){
        super();
    }

    onStart(){

        this._camerOffsetDis = 5;
        this._camerOffsetHeight = 0;
        if(this.nodeSprite.DeviceData.emptyType == 4){
            this.nodeSprite.transform.localPosition.x = 25; // 固定偏移
        }else{
            this.nodeSprite.transform.localPosition.x = -14.5; // 固定偏移
        }
        
        
        this.nodeSprite.transform.localPosition = this.nodeSprite.transform.localPosition;
    }
}