import AlertorScript from "../script/device/AlertorScript";
import DisTextScript from "../script/device/DisTextScript";
import DraughtScript from "../script/device/DraughtScript";
import HengDongScript from "../script/device/HengDongScript";
import IndicatorScript from "../script/device/IndicatorScript";
import Light01Script from "../script/device/Light01Script";
import LightGruopScript from "../script/device/LightGruopScript";
import MouseMeshScript from "../script/device/MouseMeshScript";
import QbBoardScripte from "../script/device/QbBoardScript";
import SignalLampScript from "../script/device/SignalLampScript";
import DoorScript from "../script/device/DoorScript";

import EnumType from "./EnumType";
import TunnelSprite from "./TunnelSprite";
import SensorScript from "../script/device/SensorScript";
import LightGruopScript2 from "../script/device/LightGruopScript2";
import JiFang1Script from "../script/device/JiFang1Script";

/**
 * 隧道设备基类
 */
export default class ScnenDevice extends Laya.Sprite3D {
    /**
     * 附带数据
     */
    private _deviceData:any;

    /**
     *  显示数据
     */
    private _showData:any;

    /**
     * 设备类型
     */
    private _deviceType:number;


    constructor(dt:number) {
        super();

        this._deviceType = dt;
        switch(this._deviceType){
            case EnumType.DEVICE_TYPE_QB_BOARD:
                this.loadItem("sd_qingbaoban02");
            break;
            case EnumType.DEVICE_TYPE_QB_BOARD_F:
                this.loadItem("sd_qingbaoban01");
            break;
            case EnumType.DEVICE_TYPE_DRAUGHT:
                this.loadItem("sd_wj_sd_fengji01");
            break;
            case EnumType.DEVICE_TYPE_DRAUGHT2:
                this.loadItem("sd_fengji02");
                break;
            case EnumType.DEVICE_TYPE_LIGHT01:
                this.loadItem("sd_dadeng01");    
            break;
            case EnumType.DEVICE_TYPE_LIGHT_ROAD:
                this.loadItem("ludeng01");  
            break;
            case EnumType.DEVICE_TYPE_TEXT_DIS:
                this.loadItem("textdis");
            break;
            case EnumType.DEVICE_TYPE_ALERTOR:
                this.loadItem("sd_huozhaijingbao01");
            break;
            case EnumType.DEVICE_TYPE_PHONE:
                this.loadItem("sd_dianhua01");
            break;
            case EnumType.DEVICE_TYPE_BROADCAST:
                this.loadItem("sd_guangbo01");
                break;
            case EnumType.DEVICE_TYPE_LIGHT_GRUOP:
                this.loadItem("dengzu01");
            break;
            case EnumType.DEVICE_TYPE_LIGHT_GRUOP2:
                this.loadItem("dengzu02");
                break;
            case EnumType.DEVICE_TYPE_SIGNAL_LAMP:
                this.loadItem("sd_jiaotongxinhaodeng01");
            break;
            case EnumType.DEVICE_TYPE_LAMP_INDICATOR:
                this.loadItem("sd_daoluzhishideng01");
            break;
            case EnumType.DEVICE_TYPE_HENGDONG1:
                this.loadItem("sd_hengdao02");
            break;
            case EnumType.DEVICE_TYPE_HENGDONG2:
                this.loadItem("sd_hengdao01");
            break;
            case EnumType.DEVICE_TYPE_DOOR:
                this.loadItem("suidaomen01");
                break;
            case EnumType.DEVICE_TYPE_CAM:
                this.loadItem("sd_shexiangtou01");
                break;
            case EnumType.DEVICE_TYPE_SENSOR:
                this.loadItem("chuangganqi01");
                break;
            case EnumType.DEVICE_TYPE_JIFANG1:
                this.loadItem("sd_jifang");
                break;
        }
    }

    loadItem(name:string) {
       Laya.Sprite3D.load("res/LayaScene_suidao/Conventional/" + name +".lh", Laya.Handler.create(this, this.loadMeshEnd));
       this.name = name;
    } 

    /**
     * 隧道对象
     */
    get tunnelSprite(){
        return this.parent as TunnelSprite;
    }

    /**
     * 设备类型
     */
    get deviceType(){
        return this._deviceType;
    }

    /**
     * 获取设备操作脚本
     * @returns 
     */
    getDeviceScirpt() {
        let sp3d = this.getChildAt(0);
        if(!sp3d){
            return null;
        }
        return sp3d.getComponent(MouseMeshScript);
    }

    /**
     * 模型加载结束
     * @param lh 
     */
    public loadMeshEnd(lh:Laya.Sprite3D, bBind:boolean = false){
        let sp3d = lh;
        if(!bBind){
            lh.transform.position.toDefault();
            lh.transform.localPosition.toDefault();
            sp3d = Laya.Sprite3D.instantiate(lh, this);
            sp3d.transform.localPosition.toDefault();
            sp3d.transform.localPosition = sp3d.transform.localPosition;
        }else{
            this.addChild(lh);
        }

        switch(this._deviceType){
            case EnumType.DEVICE_TYPE_QB_BOARD_F:
            case EnumType.DEVICE_TYPE_QB_BOARD:
                sp3d.addComponent(QbBoardScripte);
            break;
            case EnumType.DEVICE_TYPE_DRAUGHT2:
            case EnumType.DEVICE_TYPE_DRAUGHT:
                sp3d.addComponent(DraughtScript);
            break;
            case EnumType.DEVICE_TYPE_LIGHT_ROAD:
            case EnumType.DEVICE_TYPE_LIGHT01:
                sp3d.addComponent(Light01Script);
            break;
            case EnumType.DEVICE_TYPE_TEXT_DIS:
                sp3d.addComponent(DisTextScript);    
            break;
            case EnumType.DEVICE_TYPE_CAM:
            case EnumType.DEVICE_TYPE_ALERTOR:
            case EnumType.DEVICE_TYPE_PHONE:
            case EnumType.DEVICE_TYPE_BROADCAST:
                sp3d.addComponent(AlertorScript);    
            break;
            case EnumType.DEVICE_TYPE_LIGHT_GRUOP:
                sp3d.addComponent(LightGruopScript);  
            break;
            case EnumType.DEVICE_TYPE_LIGHT_GRUOP2:
                sp3d.addComponent(LightGruopScript2); 
                break;
            case EnumType.DEVICE_TYPE_SIGNAL_LAMP:
                sp3d.addComponent(SignalLampScript);
            break;
            case EnumType.DEVICE_TYPE_LAMP_INDICATOR:
                sp3d.addComponent(IndicatorScript);
            break;
            case EnumType.DEVICE_TYPE_HENGDONG1:
            case EnumType.DEVICE_TYPE_HENGDONG2:
                sp3d.addComponent(HengDongScript);
            break;
            case EnumType.DEVICE_TYPE_DOOR:
                sp3d.addComponent(DoorScript);
            break;
            case EnumType.DEVICE_TYPE_SENSOR:
                sp3d.addComponent(SensorScript);
            break;
            case EnumType.DEVICE_TYPE_JIFANG1:
                sp3d.addComponent(JiFang1Script);
            break;
        }
    }

    get DeviceData(){
        return this._deviceData;
    }

    public changeData(data){
        this._deviceData = data;
        let sc = this.getDeviceScirpt() as MouseMeshScript;
        if(sc){
            sc.changeData(data);
        }
    }

    get showData(){
        return this._showData;
    }

    /**
     * 切换显示数据
     * @param data 
     */
    public changeShowData(data:any){
        this._showData = data;
        let sc = this.getDeviceScirpt() as MouseMeshScript;
        if(sc){
            sc.changeData(data);
        }
    }
}