import GameGlobal from "../../Global";
import ScnenDevice from "../../logic/ScnenDevice";
import GameUI from "../GameUI";

export default class MouseMeshScript extends Laya.Script3D {
	public static TEMP_CAMERA_POS:Laya.Vector3 = new Laya.Vector3(0, 1, 0);

	/**
	 *  场景中的节点对象
	 */
    protected nodeSprite:ScnenDevice; 
	private _albedoColor = new Laya.Vector4(0.6, 0.6, 0.6, 1);

	/**
	 * 默认的X方向坐标
	 */
	protected _defaultX:number = 7;

	/**
	 * 摄像机查看偏移的高度
	 */
	protected _camerOffsetHeight:number = 5;

	/**
	 * 摄像机查看的距离
	 */
	protected _camerOffsetDis:number = 10;

	/**
	 * 摄像机查看点的高度偏移
	 */
	protected _camerLookHeight:number = 0;
    constructor(){
		super();
	}
	/**
	 * 数据被修改了
	 */
	changeData(data:any){

	}
    /**
    * 覆写3D对象组件被激活后执行，此时所有节点和组件均已创建完毕，此方法只执行一次
    */
    onAwake(){
        this.nodeSprite = this.owner.parent as ScnenDevice;

		if(this._defaultX == 10000){
			return;
		}
		let x = 0;
		// 根据坐标id设置坐标X
		if(this.nodeSprite.DeviceData){
			switch(this.nodeSprite.DeviceData.orientationLocation){
				case 18:
				case 16:
				case 17: // 左 中
				case 0:// 洞 左
					x = -this._defaultX;
				break;
				case 10: // 洞 右
				case 12: // 洞 右
				case 13: // 洞 右
				case 14: // 洞 右
				case 15: // 洞 右
					x = this._defaultX;
				break;
				case 1: //车道1
				case 2: // 车道2
				case 3: // 车道3
					x = this.nodeSprite.tunnelSprite.getRoadPosX(this.nodeSprite.DeviceData.orientationLocation);
				break;
			}
			
			// 根据位置得转换朝向
			switch(this.nodeSprite.DeviceData.orientationLocation){
				case 0:// 洞 左
				case 10: // 洞 右
				case 12: // 洞 右
				case 13: // 洞 右
				case 14: // 洞 右
				case 15: // 洞 右
				case 16: // 洞 左
				case 18: // 洞 左
				case 17: // 左 中
					if(this.nodeSprite.tunnelSprite.posType == 1){
						x = -x;
					}
					if(x < 0){
						this.nodeSprite.transform.rotate(new Laya.Vector3(0, 180, 0), true, false);
					}
				break;
			}
		}

		if(x != 0){
			this.nodeSprite.transform.localPosition.x = x;
			this.nodeSprite.transform.localPosition = this.nodeSprite.transform.localPosition;
		}
    }

    //当被鼠标点击
    onMouseClick(){
		if(GameGlobal.tunnelMgr.isClickBtn()){
			return;
		}

        //alert("点击到了我box:" + this.owner.name);
		this.cameraMoveToLook();
    }

	/**
	 * 摄像机移动到观察点
	 */
	public cameraMoveToLook(bCall:boolean = true){
		if(GameGlobal.tunnelMgr.getSelectDevice() == null){
			GameGlobal.CameraScript.clickViewBack();
		}
		let pos = MouseMeshScript.TEMP_CAMERA_POS
		this.nodeSprite.transform.getForward(pos);
		pos.x *= -this._camerOffsetDis;
		pos.y *= this._camerOffsetDis;
		pos.z *= -this._camerOffsetDis;

		pos.y += this._camerOffsetHeight;
		Laya.Vector3.add(this.nodeSprite.transform.position, pos, pos);

		this.nodeSprite.transform.position.cloneTo(GameGlobal.CameraScript.curLookPos);
		GameGlobal.CameraScript.curLookPos.y += this._camerLookHeight;
		GameGlobal.CameraScript.tweenMoveTo(pos, 500, null, GameGlobal.CameraScript.curLookPos);
		GameGlobal.tunnelMgr.setSelectDevice(this.nodeSprite, bCall);		
	}

	/**
	 * 覆写组件更新方法（相当于帧循环）
	 */
	public onUpdate():void {
		//所属脚本对象旋转更新
		//this.meshsp.transform.rotate(this._rotation, false, false)
	}
    onMouseEnter(){
        //console.log("onMouseEnter:", this.owner.name);
		this.showSelectEffect(true);
    }
    onMouseOut(){
        //console.log("onMouseOut:", this.owner.name);
		this.showSelectEffect(false);
    }

	/**
	 * 显示选中效果
	 * @param bShow 
	 */
	showSelectEffect(bShow:boolean){
		if(bShow){
			this.setSubMaterialAttr(this.owner, "albedoColor", this._albedoColor);
		}else{
			this.setSubMaterialAttr(this.owner, "albedoColor");
		}
	}

	/**
	 * 设置子材质属性
	 * @param obj 
	 * @param name 
	 * @param data 
	 * @param nameBack 
	 */
	setSubMaterialAttr(obj, name:string, data = null, nameBack:string = null){
		if(!nameBack){
			nameBack = name + "_back"
		}
		if(obj instanceof Laya.MeshSprite3D){
			let mat = obj.meshRenderer.material  as Laya.UnlitMaterial;
			if(!mat[nameBack]){
				mat[nameBack] = mat[name]
			}
			if(data){
				mat[name] = data
			}else{
				mat[name] = mat[nameBack]
			}
		}
		for(let i=0;i < obj.numChildren; ++i){
			this.setSubMaterialAttr(obj.getChildAt(i) as Laya.Sprite3D, name, data, nameBack);
		}
	}
}