import EnumType from "../../logic/EnumType";
import UtilTool from "../../util/UtilTool";
import MouseMeshScript from "./MouseMeshScript";

/***
 * 情报板
 */
export default class QbBoardScript extends MouseMeshScript {

    constructor(){
        super();
    }

        /**
    * 覆写3D对象组件被激活后执行，此时所有节点和组件均已创建完毕，此方法只执行一次
    */
    onStart(){
        super.onAwake();
		this._camerOffsetHeight = 15;
		this._camerLookHeight = 4;

        let bExit = this.nodeSprite.tunnelSprite.posType == 1;
		// 临时设置到场景中的位置
        if(this.nodeSprite.deviceType == EnumType.DEVICE_TYPE_QB_BOARD_F){
            this.nodeSprite.transform.localPosition.x = 8.6;
            //this.nodeSprite.transform.localPosition.z = 32;
            this.showText(this.nodeSprite.DeviceData.contentText, 50);
            if(bExit){
                this.nodeSprite.transform.localPosition.x = 7.6;
            }

        }else{
            let sp = this.owner as Laya.Sprite3D;
            sp.transform.localPosition.y = 7.63;
		    sp.transform.localPosition = sp.transform.localPosition;
            this.nodeSprite.transform.localPosition.x = 0;
            //this.nodeSprite.transform.localPosition.z = 22;
            this.showText(this.nodeSprite.DeviceData.contentText, 50);
        }

        if(bExit){ // 在出口要旋转
            this.nodeSprite.transform.localPosition.x = -this.nodeSprite.transform.localPosition.x;
            this.nodeSprite.transform.rotate(new Laya.Vector3(0, 180, 0), true, false);
        }
		
		this.nodeSprite.transform.localPosition = this.nodeSprite.transform.localPosition;
   }

   /**
    * 设置情报板内容
    * @param str 
    */
   showText(str:string, ftSize:number){
       if(!str || str == ""){
           return;
       }
        let meshsp = this.owner.getChildByName("textinput") as Laya.MeshSprite3D;
        let anm:Laya.Animator = meshsp.getComponent(Laya.Animator);
        let tex = UtilTool.createText3D(str,50, "rgb(250,0,0)", "Microsoft YaHei");
        let mat = meshsp.meshRenderer.material as Laya.EffectMaterial
        mat.texture = tex;
        
        if(anm){
            let w = str.replace(/[\u0391-\uFFE5]/g,"aa").length; // 文字数量，中文占2个
            anm.speed = 10 / w; // 根据数字匹配对应的速度
        }
   }
}
