import GameGlobal from "../Global";
import CameraScript from "./CameraScript";
import Transform3D = Laya.Transform3D;
import Vector3 = Laya.Vector3;
import Quaternion = Laya.Quaternion;


export default class ModelViewer extends CameraScript {
    // Text m_debugTip;
    public canRotation_X: boolean = true;
    public canRotation_Y: boolean = true;
    public canScale: boolean = true;
    protected  _tempVector3:Laya.Vector3 = new Laya.Vector3();
    /// <summary>
    /// Around center.
    /// </summary>
    //public target: Transform3D;
    public AroundPos: Vector3 = new Vector3();
    /// <summary>
    /// Settings of mouse button, pointer and scrollwheel.
    /// </summary>
    public mouseSettings: MouseSettings = new MouseSettings(0, 0.3, 0.3);

    /// <summary>
    /// Range limit of angle.
    /// </summary>
    public angleRange: MyRange = new MyRange(-360, 360);

    /// <summary>
    /// Range limit of distance.
    /// </summary>
    public distanceRange: MyRange = new MyRange(1, 200);

    /// <summary>
    /// Damper for move and rotate.
    /// </summary>
    ///[Range(0, 10)]
    public damper: number = 5;

    /// <summary>
    /// Camera current angls.
    /// </summary>
    public CurrentAngles: Vector3 = new Vector3();
    public CurrentAnglesTemp: Vector3 = new Vector3();
    /// <summary>
    /// Current distance from camera to target.
    /// </summary>
    public CurrentDistance: number;

    /// <summary>
    /// Camera target angls.
    /// </summary>
    protected targetAngles: Vector3 = new Vector3();


    /// <summary>
    /// Target distance from camera to target.
    /// </summary>
    protected targetDistance: number;

    protected  moveV3:Laya.Vector3 = new Laya.Vector3;
    private _keydowning:boolean = false;
    protected  yawPitchRoll:Laya.Vector3 = new Laya.Vector3();
    protected  rotaionSpeed:number = 0.00008;
    public btnMoveValue:number = 0;

    public easyMode:boolean = true;
    constructor() {
        super();
    }
    public transform: Transform3D;

    onStart(): void {
        super.onStart();
        GameGlobal.CameraScript = this;
        if(this._minViewZ == 0){
            this._minViewZ = -GameGlobal.tunnelMgr.getMaxLenth();
        }
        this.initTransform();
        Laya.stage.on(Laya.Event.MOUSE_DOWN, this, this.mouseDown);
        Laya.stage.on(Laya.Event.MOUSE_MOVE, this, this.mouseMove);
        Laya.stage.on(Laya.Event.MOUSE_UP, this, this.mouseUp);
        Laya.stage.on(Laya.Event.MOUSE_WHEEL, this, this.mouseWheel);
        Laya.stage.on(Laya.Event.RIGHT_MOUSE_DOWN, this, this.rightMouseDown);
        Laya.stage.on(Laya.Event.RIGHT_MOUSE_UP, this, this.rightMouseUp);
        Laya.stage.on(Laya.Event.BLUR, this, this.blurEvent);
        Laya.stage.on(Laya.Event.MOUSE_OUT, this, this.blurEvent);
    }

    blurEvent(){
        this.mouseRunning = false;
        this.bRightMouseDown = false;
        GameGlobal.tunnelMgr.mouseClickUp();
    }

    /**
     * 初始化一次计算视角
     * @param bResetLook
     */
    initTransform(bResetLook:boolean= false){
        this.transform = this.camera.transform;
        this.CurrentAngles.setValue(-this.transform.rotationEuler.x, this.transform.rotationEuler.y, this.transform.rotationEuler.z);
        this.targetAngles.setValue(-this.transform.rotationEuler.x, this.transform.rotationEuler.y, this.transform.rotationEuler.z);

        if(!bResetLook){
            if(this.curLookPos){
                this.curLookPos.cloneTo(this.AroundPos);
            }else{
                this.AroundPos.setValue(0, 0, 0);
            }
        }

        this.CurrentDistance = Vector3.distance(this.transform.position, this.AroundPos);
        this.targetDistance = Vector3.distance(this.transform.position, this.AroundPos);
        let dismax = this.distanceRange.max;
        let dis = 30;
        // 如果是高难度模式，而且没有选中模型 直接计算一个新的非常近的look点
        if(!this.easyMode && GameGlobal.tunnelMgr.getSelectDevice() == null){
            dismax = 2;
            dis = 1;
        }
        if(bResetLook || this.CurrentDistance > dismax){
            Vector3.scale(this.GetForward, -dis, this.tempV)
            Vector3.subtract(this.transform.position, this.tempV, this.tempV1)
            this.tempV1.cloneTo(this.AroundPos);
            this.tempV1.cloneTo(this.curLookPos);
        }
        this.CurrentDistance = Vector3.distance(this.transform.position, this.AroundPos);
        this.targetDistance = Vector3.distance(this.transform.position, this.AroundPos);
    }

    moveOver(){
        super.moveOver();
        this.initTransform();
    }

    /**
     *  更新朝向
     */
     protected  _updateRotation():void {
        if (Math.abs(this.yawPitchRoll.y) < 1.50) {
            Laya.Quaternion.createFromYawPitchRoll(this.yawPitchRoll.x, this.yawPitchRoll.y, this.yawPitchRoll.z, this.tempRotationZ);
            this.tempRotationZ.cloneTo(this.camera.transform.localRotation);
            this.camera.transform.localRotation = this.camera.transform.localRotation;
        }
    }
    onUpdate(): void {
        super.onUpdate();
        if(this._moveing){
            return;
        }
        var elapsedTime:number = Laya.timer.delta;
        if(this.btnMoveValue !=0){
            this.btnClickMoveZ(0.01 * elapsedTime * this.btnMoveValue);
            return;
        }
        let rate = Laya.KeyBoardManager.hasKeyDown(16) ? 3:1;
        if(!GameGlobal.tunnelMgr.getSelectDevice()){
            Laya.KeyBoardManager.hasKeyDown(38) && this.btnClickMoveZ(-0.03 * elapsedTime * rate);//上
            Laya.KeyBoardManager.hasKeyDown(40) && this.btnClickMoveZ(0.03 * elapsedTime * rate);//下
        }
        if (!isNaN(this.lastMouseX) && !isNaN(this.lastMouseY) && (GameGlobal.tunnelMgr.getSelectDevice() || !this.easyMode)) {
            let keydown = this._keydowning;
            this._keydowning = false;

            Laya.KeyBoardManager.hasKeyDown(87) && this.moveForward(0, 1 * rate);//W
            Laya.KeyBoardManager.hasKeyDown(83) && this.moveForward(0, -1 * rate);//S
            Laya.KeyBoardManager.hasKeyDown(65) && this.moveForward(1 * rate, 0);//A
            Laya.KeyBoardManager.hasKeyDown(68) && this.moveForward(-1 * rate, 0);//D
            Laya.KeyBoardManager.hasKeyDown(81) && this.moveVertical(0.01 * elapsedTime * rate);//Q
            Laya.KeyBoardManager.hasKeyDown(69) && this.moveVertical(-0.01 * elapsedTime * rate);//E

            if(keydown && !this._keydowning){
                this.initTransform(true);
            }
        }

        if(this._keydowning){
            //let pos = this.camera.transform.position;
            //console.log("cam pos:", pos.x, pos.y, pos.z)
            GameGlobal.tunnelMgr.setSelectDevice(null);
            return;
        }
        let offsetX = this.GetAxisX;
        let offsetY = this.GetAxisY;

        // if(this.mouseRunning && !GameGlobal.tunnelMgr.getSelectDevice() && !this.easyMode){ // 左键控制旋转方向
        //     var yprElem:Laya.Vector3 = this.yawPitchRoll;
        //     yprElem.x -= offsetX * this.rotaionSpeed * elapsedTime;
        //     yprElem.y -= offsetY * this.rotaionSpeed * elapsedTime;
        //     this._updateRotation();
        //     this.initTransform(true);
            
        //     this.lastMouseX = Laya.stage.mouseX;
        //     this.lastMouseY = Laya.stage.mouseY;
        //     return;
        // }

        // 最后是选中模型的情况下视角转动
        this.AroundByMouseInput();
        if (!this.canRotation_X) this.targetAngles.y = 0;
        if (!this.canRotation_Y) this.targetAngles.x = 0;

        if(!this.easyMode && this.bRightMouseDown){ // 右键控制平移移动
            let r = this.transform.rotationEuler.y / 180 * Math.PI
            let sr = Math.sin(r);
            let cr = Math.cos(r);
            let dis = this.deltaTime * 2;
            this.moveV3.z = -(offsetY * cr -  offsetX* sr) * dis;
            this.moveV3.x = -(offsetY * sr +  offsetX* cr) * dis;
            this.transform.translate(this.moveV3, false);
            Laya.Vector3.add(this.AroundPos, this.moveV3, this.AroundPos);
            Laya.Vector3.add(this.curLookPos, this.moveV3, this.curLookPos);
            GameGlobal.tunnelMgr.setSelectDevice(null);
        }else{
            //Lerp.
            this.CurrentAngles = this.LerpVector3(this.CurrentAngles, this.targetAngles, this.deltaTime * this.damper);
            this.CurrentDistance = this.LerpNum(this.CurrentDistance, this.targetDistance, this.deltaTime * this.damper);
            //Update transform position and rotation.
            // Quaternion.createFromYawPitchRoll(this.CurrentAngles.y,this.CurrentAngles.x,this.CurrentAngles.z,this.tempRotationZ)
            // this.transform.rotation = this.tempRotationZ;
            this.CurrentAnglesTemp.x = -this.CurrentAngles.x;
            this.CurrentAnglesTemp.y = this.CurrentAngles.y;
            this.CurrentAnglesTemp.z = this.CurrentAngles.z;
            this.transform.rotationEuler = this.CurrentAnglesTemp;
            Vector3.scale(this.GetForward, this.CurrentDistance, this.tempV);
            Vector3.subtract(this.AroundPos, this.tempV, this.tempV1);
            this.tempV1.y = Math.max(0.2, this.tempV1.y);
            this.transform.position = this.tempV1;
        }

        this.lastMouseX = Laya.stage.mouseX;
        this.lastMouseY = Laya.stage.mouseY;

    }
    /**
     * 向前移动。
     * @param distance 移动距离。
     */
     public  moveForward(offsetX:number, offsetY:number):void {
        let r = this.transform.rotationEuler.y / 180 * Math.PI
        let sr = Math.sin(r);
        let cr = Math.cos(r);
        let dis = this.deltaTime * 20;
        this.moveV3.z = -(offsetY * cr -  offsetX* sr) * dis;
        this.moveV3.x = -(offsetY * sr +  offsetX* cr) * dis;
        this.transform.translate(this.moveV3, false);
        Laya.Vector3.add(this.AroundPos, this.moveV3, this.AroundPos);
        Laya.Vector3.add(this.curLookPos, this.moveV3, this.curLookPos);

        // this._tempVector3.x = this._tempVector3.y = 0;
        // this._tempVector3.z = distance;
        // this.camera.transform.translate(this._tempVector3, false);
        this._keydowning = true;
    }

    /**
     * 向上移动。
     * @param distance 移动距离。
     */
    public moveVertical(distance:number):void {
        this._tempVector3.x = this._tempVector3.z = 0;
        this._tempVector3.y = distance;
        this.camera.transform.translate(this._tempVector3, false);
        this._keydowning = true;
    }

    /**
     * 按键移动
     * @param distance 
     */
    public btnClickMoveZ(distance:number):void {
        this._tempVector3.x = this._tempVector3.y = 0;
        this._tempVector3.z = distance;
        this.camera.transform.translate(this._tempVector3, false);
        this.curLookPos.z += distance;
        this.initTransform();
    }

    public get GetAxisX(): number {
        return Laya.stage.mouseX - this.lastMouseX;
    }
    public get GetAxisY(): number {
        return Laya.stage.mouseY - this.lastMouseY;
    }
    public get deltaTime(): number {
        return Laya.timer.delta / 1000;
    }

    public FORWORD: Vector3 = new Vector3();
    public get GetForward(): Vector3 {
        this.transform.getForward(this.FORWORD);
        return this.FORWORD
    }

    public deltaWheel: number = 0;
    protected lastMouseX: number = 0;
    protected lastMouseY: number = 0;
    protected mouseRunning: boolean = false;
    protected  bRightMouseDown:Boolean;
    protected mouseDown(e: Laya.Event): void {
        if(e.target.parent != Laya.Scene.root){
            return;
        }
        this.mouseRunning = true;
        this.initTransform();
        this.camera.transform.localRotation.getYawPitchRoll(this.yawPitchRoll);
    }
    myevent: Laya.Event;
    protected mouseMove(e: Laya.Event): void {
        this.myevent = e;
        Laya.Browser.window.focus();
    }

    protected mouseWheel(e: Laya.Event): void {
        if(!this.easyMode || GameGlobal.tunnelMgr.getSelectDevice()){
            this.deltaWheel = e.delta;
        }
    }
    protected mouseUp(e: Laya.Event): void {
        this.mouseRunning = false;
        Laya.Browser.window.focus();
    }
    protected rightMouseDown(e:Laya.Event):void{
        if(GameGlobal.tunnelMgr.getSelectDevice()){
            GameGlobal.tunnelMgr.setSelectDevice(null);
            GameGlobal.CameraScript.clickViewReset();
            return;
        }
        this.bRightMouseDown = true;
    }

    protected rightMouseUp(e:Laya.Event){
        this.lastMouseX = Laya.stage.mouseX;
        this.lastMouseY = Laya.stage.mouseY;
        this.bRightMouseDown = false;
        this.initTransform();
    }

    protected Clamp(num, min, max): number {
        if (num < min) {
            return min;
        } else if (num > max) {
            return max;
        } else {
            return num;
        }
    }

    protected LerpVector3(min: Vector3, max: Vector3, t: number): Vector3 {
        var vec: Vector3 = new Vector3();
        vec.x = min.x + t * (max.x - min.x);
        vec.y = min.y + t * (max.y - min.y);
        vec.z = min.z + t * (max.z - min.z);
        return vec;
    }
    protected LerpNum(min: number, max: number, t: number): number {
        return min + t * (max - min);
    }

    tempRotationZ: Quaternion = new Quaternion();
    tempV: Vector3 = new Vector3();
    tempV1: Vector3 = new Vector3();


    /// <summary>
    /// Camera around target by mouse input.
    /// </summary>
    protected AroundByMouseInput(): void {
        if (this.mouseRunning) {
            //Mouse pointer.
            this.targetAngles.y -= (this.GetAxisX * this.mouseSettings.pointerSensitivity);
            this.targetAngles.x += (this.GetAxisY * this.mouseSettings.pointerSensitivity);
            //Range.
            this.targetAngles.x = this.Clamp(this.targetAngles.x, this.angleRange.min, this.angleRange.max);
        }
        //Mouse scrollwheel.
        if (this.canScale) {
            this.targetDistance -= this.deltaWheel * this.mouseSettings.wheelSensitivity;
            this.deltaWheel = 0;
            this.targetDistance = this.Clamp(this.targetDistance, this.distanceRange.min, this.distanceRange.max);
        }
    }
}

class MouseSettings {
    /// <summary>
    /// ID of mouse button.
    /// </summary>
    public mouseButtonID: number;

    /// <summary>
    /// Sensitivity of mouse pointer.
    /// </summary>
    public pointerSensitivity: number;

    /// <summary>
    /// Sensitivity of mouse ScrollWheel.
    /// </summary>
    public wheelSensitivity: number;

    /// <summary>
    /// Constructor.
    /// </summary>
    /// <param name="mouseButtonID">ID of mouse button.</param>
    /// <param name="pointerSensitivity">Sensitivity of mouse pointer.</param>
    /// <param name="wheelSensitivity">Sensitivity of mouse ScrollWheel.</param>
    constructor(mouseButtonID: number, pointerSensitivity: number, wheelSensitivity: number) {
        this.mouseButtonID = mouseButtonID;
        this.pointerSensitivity = pointerSensitivity;
        this.wheelSensitivity = wheelSensitivity;
    }
}

/// <summary>
/// Range form min to max.
/// </summary>
class MyRange {
    /// <summary>
    /// Min value of range.
    /// </summary>
    public min: number;

    /// <summary>
    /// Max value of range.
    /// </summary>
    public max: number;

    /// <summary>
    /// Constructor.
    /// </summary>
    /// <param name="min">Min value of range.</param>
    /// <param name="max">Max value of range.</param>
    constructor(min: number, max: number) {
        this.min = min;
        this.max = max;
    }
}


/// <summary>
/// Rectangle area on plane.
/// </summary>
class PlaneArea {
    /// <summary>
    /// Center of area.
    /// </summary>
    public center: Transform3D;

    /// <summary>
    /// Width of area.
    /// </summary>
    public width: number;

    /// <summary>
    /// Length of area.
    /// </summary>
    public length: number;

    /// <summary>
    /// Constructor.
    /// </summary>
    /// <param name="center">Center of area.</param>
    /// <param name="width">Width of area.</param>
    /// <param name="length">Length of area.</param>
    constructor(center: Transform3D, width: number, length: number) {
        this.center = center;
        this.width = width;
        this.length = length;
    }
}