import GameGlobal from "../Global";
import GameUI from "../script/GameUI";
import ScnenDevice from "./ScnenDevice";
import TunnelSprite from "./TunnelSprite";

/**
 * 隧道管理
 */
export default class TunnelMgr {
    /**
     * 单隧道还是双隧道
     */
    public TunnelCount:number = 2;

    /** 
     * 隧道显示分段 0 or 1
     */
    public showMode:number = 0;

    /**
     * 几车道
     */
    public laneNums:number = 2;
    /**
     * 设备列表
     */
    private _deviceList:Map<string, ScnenDevice> = new Map();

    /**
     * 隧道初始化数据
     */
    public InitData:any;

    /**
     * 左洞
     */
     public _leftTunnelSprite:TunnelSprite;

      /**
     * 右洞
     */
    public _rightTunnelSprite:TunnelSprite;

    /**
     * 所有视点
     */
    private _viewList:Array<Laya.Sprite3D>;

    /**
     * 最长的隧道长度
     */
    private _maxLength:number = 0;

    /**
     * 最小的其实桩位
     */
    private _minStartPos:number = 0;

    /**
     * 当前选中设备
     */
    private _curSelectDevice:ScnenDevice;

    private _curClickTarget:any;

    private _clickLastTime:number;
    constructor(){

    }

    /**
     * 初始化隧道
     */
    init(scn){
      Laya.stage.on(Laya.Event.MOUSE_DOWN, this, this.mouseClick);
      Laya.stage.on(Laya.Event.MOUSE_UP, this, this.mouseClickUp);
    }

    mouseClick(e:Laya.Event){
      this._curClickTarget = e.target;
      if(this._curClickTarget instanceof GameUI){
        this._clickLastTime = Date.now();
      }
    }
    mouseClickUp(){
      this._curClickTarget = null;
    }

    isClickBtn(){
      let ntime =Date.now() - this._clickLastTime
      return ntime > 500;//!(this._curClickTarget instanceof GameUI) || this._curClickTarget == null;
    }

    getMaxLenth(){
      return this._maxLength;
    }
    getMinStartPos(){
      return this._minStartPos;
    }

    /**
     * 初始化隧道信息
     * @param data
     */
    loadTunnel(data:any, addData:any){
      if(data.lengthScale){
        GameGlobal.SHOW_SCALE = data.lengthScale;
      }
      if(data.showMode){
        this.showMode = data.showMode;
      }
      if(data.showDebug){
        GameGlobal.SHOW_DEBUG = data.showDebug == 1;
      }
      this.InitData = data;
      this.laneNums = data.laneNums;

      if(data.singleDoubleType == 2){
        data.startStakeMarkRight = data.startStakeMark;
        data.rightHoleLength = data.leftHoleLength;
      }else if(data.singleDoubleType == 1){
        data.startStakeMark = data.startStakeMarkRight;
        data.leftHoleLength = data.rightHoleLength;
      }
      this._maxLength = Math.max(data.leftHoleLength, data.rightHoleLength);
      this._minStartPos = Math.min(data.startStakeMark,  data.startStakeMarkRight);

      // 左洞
      let ts0 = new TunnelSprite();
      GameGlobal.GameScene.addChild(ts0);
      if(data.singleDoubleType == 3 || data.singleDoubleType == 2){
        ts0._bActive = true;
      }else{
        ts0._bActive = false;
      }
      
      ts0.RoadCount = data.laneNums;
      ts0.startMark = data.startStakeMark;
      ts0.init(data.leftHoleLength * GameGlobal.SHOW_SCALE, 1, addData);
      ts0.setDirText(data.leftHoleDirection);
      if(ts0._bActive){
        ts0.transform.position.z = (data.startStakeMarkRight - data.startStakeMark) * GameGlobal.SHOW_SCALE;
      }
      ts0.transform.position = ts0.transform.position;
      this._leftTunnelSprite = ts0;

      // 右洞
      let ts1 = new TunnelSprite();
      GameGlobal.GameScene.addChild(ts1);
      if(data.singleDoubleType == 3 || data.singleDoubleType == 1){
        ts1._bActive = true;
      }else{
        ts1._bActive = false;
      }
      ts1.RoadCount = data.laneNums;
      ts1.startMark = data.startStakeMarkRight;
      ts1.init(data.rightHoleLength * GameGlobal.SHOW_SCALE, 0, addData);
      ts1.setDirText(data.rightHoleDirection);
      this._rightTunnelSprite = ts1;
      if(GameGlobal.CameraScript){
        GameGlobal.CameraScript._minViewZ = -this.getMaxLenth()
      }
    }


    /**
     * 根据唯一id查找设备
     * @param id 
     * @returns 
     */
    findDevice(id:string){
      return this._deviceList[id];
    }

    /**
     * 添加隧道设备
     * @param data 
     */
    addDevice(data:any){
      let device:ScnenDevice = this.findDevice(data.id);
      if(device){
        device.changeData(data);
        return;
      }
      if(data.orientationLocation == 17){
        data.leftRightFlag = 2;
    }
      if(data.leftRightFlag == 1){
        device = this._rightTunnelSprite.addDevice(data);
      }else{
        device = this._leftTunnelSprite.addDevice(data);
      }
      this._deviceList[data.id] = device;
    }
    /**
     * 批量添加设备
     * @param ary 
     */
    addDevices(ary:any){
      for(let i = 0; i < ary.length; ++i){
        this.addDevice(ary[i]);
      }
    }


    /**
     * 获取巡游视点
     * @param i 
     * @returns 
     */
    getCurView(index:number){
      if(!this._viewList){
        let obj1 = this._rightTunnelSprite.getStartView() as Laya.Sprite3D;
        let obj2 = this._leftTunnelSprite.getEndView() as Laya.Sprite3D;
        if(!obj1 && !obj2){
          console.log("资源还没加载结束，不能巡游");
          return;
        }
        this._viewList = new Array();
        if(obj1){
          this._viewList.push(obj1);
          this._viewList.push(this._rightTunnelSprite.getEndView() as Laya.Sprite3D);
        }
        if(obj2){
          this._viewList.push(obj2);
          this._viewList.push(this._leftTunnelSprite.getStartView() as Laya.Sprite3D);
        }
      }
      if(this._viewList.length <= index){
        return null;
      }
      return this._viewList[index];
    }

    /**
     * 设置当前选中设备
     * @param obj 
     */
    setSelectDevice(obj:ScnenDevice, bCall:boolean = true){
      if(this._curSelectDevice == obj && obj == null){
        return;
      }
      this._curSelectDevice = obj;
      GameUI.instance.setMoveBtnState(obj == null);
      if(!obj){
        GameUI.instance.textSelect.text = "当前选中：无";
        return;
      }
      let str = obj.name;
      if(bCall && obj.DeviceData){
        str = JSON.stringify(obj.DeviceData);
  
        let msg = {
          msgType:"clickDevice",
          msgData:obj.DeviceData,
        }
        Laya.Browser.window.parent.postMessage(msg);
      }
  
      GameUI.instance.textSelect.text = "当前选中：" + str;
    }

    getSelectDevice(){
      return this._curSelectDevice;
    }

}