import MouseMeshScript from "./MouseMeshScript";

export default class HengDongScript extends MouseMeshScript{
    constructor(){
        super();
    }

    onAwake(){
        this._defaultX = 7;
        super.onAwake();
    }

    onStart(){

        this._camerOffsetDis = 5;
        this._camerOffsetHeight = 0;

        //this.nodeSprite.transform.localPosition.y = 0; // 固定高度偏移
        this.nodeSprite.transform.localPosition.x = -14.359;
        
        this.nodeSprite.transform.localPosition = this.nodeSprite.transform.localPosition;
    }
}