import ScnenDevice from "../../logic/ScnenDevice";
import UtilTool from "../../util/UtilTool";
import MouseMeshScript from "./MouseMeshScript";


export default class LightGruopScript extends MouseMeshScript{

    constructor(){
        super();
    }
    onAwake(){
        this.nodeSprite = this.owner.parent as ScnenDevice;
        this._defaultX = 6.8;
        if(this.nodeSprite.DeviceData.orientationLocation == 17){
            this._defaultX = 16;
        }
        super.onAwake();
    }

    onStart(){
        this._camerOffsetDis = 5;
        this._camerOffsetHeight = 0;

        this.nodeSprite.transform.localPosition.y = 3.2; // 固定高度偏移
        this.nodeSprite.transform.localPosition = this.nodeSprite.transform.localPosition;

        if(this.nodeSprite.DeviceData.orientationLocation != 17){
            this.nodeSprite.transform.rotate(new Laya.Vector3(0, -90, 0), true, false);
        }else{
            // 横洞中的距离单独处理
            let ms = this.owner as Laya.Sprite3D;
            ms.transform.localPosition.z = -4;
            ms.transform.localPosition = ms.transform.localPosition;
        }
        

        this.changeData();
    }

    setOpen(bOpen:boolean){
        let sp3 = this.owner as Laya.Sprite3D;
        for(let i = 0; i < 6; i ++){
            let light = sp3.getChildAt(i);
            let sp0 = light.getChildAt(0) as Laya.Sprite3D;
            let sp1 = light.getChildAt(1) as Laya.Sprite3D;
            sp0.active = bOpen;
            sp1.active = !bOpen;
        }
    }

    changeData(){
        this.setOpen(this.nodeSprite.DeviceData.workMode == 1);
    }
}