import ScnenDevice from "../../logic/ScnenDevice";
import MouseMeshScript from "./MouseMeshScript";


export default class LightGruopScript2 extends MouseMeshScript{

    constructor(){
        super();
    }
    onAwake(){

        this._defaultX = 4;
        super.onAwake();
    }

    onStart(){
        this._camerOffsetDis = 5;
        this._camerOffsetHeight = 0;

        this.nodeSprite.transform.localPosition.y = 7; // 固定高度偏移
        this.nodeSprite.transform.localPosition = this.nodeSprite.transform.localPosition;

        this.nodeSprite.transform.rotate(new Laya.Vector3(0, -90, 0), true, false);
        this.changeData();
    }

    setOpen(bOpen:boolean){
        let sp3 = this.owner as Laya.Sprite3D;
        for(let i = 0; i < 6; i ++){
            let light = sp3.getChildAt(i).getChildByName("sd_dadeng02") as Laya.MeshSprite3D;
            light.getChildAt(0).active = bOpen;
            let mat = light.meshRenderer.material as Laya.EffectMaterial;
            if(bOpen){
                mat.colorA = 1;
            }else{
                mat.colorA = 0.1;
            }
        }
    }

    changeData(){
        this.setOpen(this.nodeSprite.DeviceData.workMode == 1);
    }
}