/**
* 工具类
*/
export default class UtilTool {
	/**
	* 创建3D文字纹理
	* @param str 文字内容
	* @param fontSize 文字大小
	*/
	public static createText3D(str:string, fontSize:number = 50, ftClolor:string = "rgb(240,0,0)", font:string = "宋体"){
		let textary = str.split(/[\n]/);
		let maxw = 0;
		let linecount = textary.length;
		textary.forEach(t => {
		let w = t.replace(/[\u0391-\uFFE5]/g,"aa").length / 2 * (fontSize + 2);
		maxw = Math.max(w, maxw);
		});
		let width = maxw;//str.replace(/[\u0391-\uFFE5]/g,"aa").length / 2 * (fontSize + 2);
		width = Math.max(width, fontSize * 3);

		//画布cavans
		let cav = Laya.Browser.createElement("canvas");
		var cxt = cav.getContext("2d");
		cav.width = width;
		cav.height = fontSize * linecount + 4;
		cxt.fillStyle = ftClolor;
		cxt.font = "bold "+ fontSize + "px " + font;
		cxt.textAlign = "left";//文本的对齐方式
		cxt.textBaseline = "top";//文本相对于起点的位置

		for(let i = 0; i < linecount; ++i){
			cxt.fillText(textary[i], 0, i * fontSize+4); //设置文字,位置
		}

		// 生成纹理
		let texture2D = new Laya.Texture2D(128, 128);
		texture2D.loadImageSource(cav);

		Laya.Browser.removeElement(cav);
		return texture2D;
	}


}
