import TunnelMgr from "./logic/TunnelMgr";
import ModelViewer from "./script/ModelViewer";

export default class GameGlobal {

    /**
     *  隧道距离显示比例
     */
    public static SHOW_SCALE:number = 1;

    /**
     *  是否显示调试信息
     */
    public static SHOW_DEBUG:boolean = false;

    /** 游戏的主场景 */
    public static GameScene:Laya.Scene3D;

    /** 游戏的主摄像机 */
    public static GameCamera:Laya.Camera;

     /** 游戏的主摄像机控制脚本 */
    public static CameraScript:ModelViewer;


    /**
     * 隧道管理
     */
    public static tunnelMgr:TunnelMgr = new TunnelMgr();
}