/**
 * 马路节点 用于缩放和拼接
 */

import GameGlobal from "../Global";

 export default class RoadNode extends Laya.Sprite3D{

    private _roadID:number = 0;
    /**
     * 隧道节点类型
     */
    private _nodeType:number = 0;
    /**
     * 默认长度
     */
    private _defaultLength:number = 5;

    /**
     * 当前显示长度
     */
    private _showLength:number = 50;

    /**
     * 模型对象
     */
    private _meshNode:Laya.Sprite3D;

    /**
     * 上一个节点
     */
    public _lastNode:RoadNode;

    /**
     * 下一个节点
     */
    public _nextNode:RoadNode;
    
    /**
     * 附加数据
     */
    public addData:any;

    public loaded:Laya.Handler;

    constructor(nodeType:number, lastNode:RoadNode = null){
        super();
        this._lastNode = lastNode;
        if(lastNode){
            lastNode._nextNode = this;
        }
        this._nodeType = nodeType;
        let filename = "sd_suidao01";
        switch(nodeType){
            case 0: // 可缩放隧道节点

                break;
            case 1: // 隧道头 固定长度
                filename = "sd_rukou01";
                this._defaultLength = 18.086*2;
                break;
            case 2: // 隧道尾 固定长度
                filename = "sd_rukou02";
                this._defaultLength = 18.086*2;
                break;
            case 3: // 空洞 只有路 没有拱门
                filename = "sd_suidao02";
                break;
            case 4: // 人行带门 左 固定长度
                filename = "sd_suidao02_xiaoyou";
            break;
            case 5: // 人行带门 右 固定长度
                filename = "sd_suidao02_xiaozuo";
            break;
            case 6: // 车行带门 左 固定长度
                filename = "sd_suidao02_you";
            break;
            case 7: // 车行带门 右 固定长度
                filename = "sd_suidao02_zuo";
            break;
        }
        this.showLength = this._defaultLength;
        Laya.Sprite3D.load("res/LayaScene_suidao/Conventional/" + filename + ".lh", Laya.Handler.create(this, this.loadMeshEnd));
        this.name = filename;
        
    }

        /**
     * 模型加载结束
     * @param lh 
     */
    protected loadMeshEnd(lh:Laya.Sprite3D){
        lh.transform.position.toDefault();
        lh.transform.localPosition.toDefault();
        this._meshNode = Laya.Sprite3D.instantiate(lh, this);
        this._meshNode.transform.localScale.setValue(1, 1, 1);
        this._meshNode.transform.localPosition.toDefault();

        switch(this._nodeType){
            case 1:
                this._meshNode.transform.localPosition.setValue(0.31956, -0.0073903, 0);
                break;
            case 2:
                this._meshNode.transform.localPosition.setValue(0.31956, -0.0073903, 0);
               // this._meshNode.transform.localPosition.setValue(-0.4, 5.23, 0);
                //this._meshNode.transform.rotate(new Laya.Vector3(0, 0, 180), true, false);
                break;
            case 4:
            case 5:
            case 6:
            case 7:
                //this._meshNode.transform.localPosition.setValue(0, 0, 0.003);
                break;
            case 3:
            case 0: // 缩放节点需要处理UV的布局
                this.setSubMaterialAttr(this._meshNode.getChildByName("sd_qiangbi01"));
                this.setSubMaterialAttr(this._meshNode.getChildByName("sd_qiangbi049"));
                this.setSubMaterialAttr(this._meshNode.getChildByName("sd_qiangbi051"));
                this.setSubMaterialAttr(this._meshNode.getChildByName("sd_lumian01-3"));
                this.setSubMaterialAttr(this._meshNode.getChildByName("sd_lumian01-2"));
            break;
        }
        this._meshNode.transform.localScale = this._meshNode.transform.localScale;
        this._meshNode.transform.localPosition = this._meshNode.transform.localPosition;

        if(this.loaded){
            this.loaded.run();
            this.loaded = null;
        }
        let lumian = this._meshNode.getChildByName("sd_lumian01-3");
        if(lumian){
            lumian.active = GameGlobal.tunnelMgr.laneNums == 3;
        }
        let lumian1 = this._meshNode.getChildByName("sd_lumian01-2");
        if(lumian1){
            lumian1.active = GameGlobal.tunnelMgr.laneNums == 2;
        }
        lumian = this._meshNode.getChildByName("sd_rukoudimian01-3");
        if(lumian){
            lumian.active = GameGlobal.tunnelMgr.laneNums == 3;
        }
        lumian1 = this._meshNode.getChildByName("sd_rukoudimian01-2");
        if(lumian1){
            lumian1.active = GameGlobal.tunnelMgr.laneNums == 2;
        }
        lumian = this._meshNode.getChildByName("sd_rukoudimian02-3");
        if(lumian){
            lumian.active = GameGlobal.tunnelMgr.laneNums == 3;
        }
        lumian1 = this._meshNode.getChildByName("sd_rukoudimian02-2");
        if(lumian1){
            lumian1.active = GameGlobal.tunnelMgr.laneNums == 2;
        }
        let view = this._meshNode.getChildByName("view")
        if(view){
            view.active = false;
        }
    }

    /**
     * 当前显示长度
     */
    public get showLength (){
        return this._showLength;
    }

    /**
     * 设置当前显示的长度，会自动计算偏移点
     * @param len 
     */
    public set showLength (len:number){
        this._showLength = len;
        // if(this._nodeType != 0){
        //     return;
        // }
        let scale = len / this._defaultLength; // 根据长度计算出比例缩放
        this.transform.localScale.z = scale;

        this.transform.localScale = this.transform.localScale;
        let z = - len / 2;
        if(this._lastNode){
            z += this._lastNode.getNextNodePos(); // 紧跟上一个节点
        }else{ // 没有头，则放0的外面开始
            if(this._nodeType == 3){
                z = -len/2 + 35; // 空洞头要加个偏移
            }else{
                z = len/2;
            }
            
        }
        this.transform.localPosition.z = z;
        if(this._roadID != 0){
            this.transform.localPosition.x = this._roadID * -50;
        }
        this.transform.localPosition = this.transform.localPosition;
    }

    /**
     * 获取下一个节点的位置
     */
    public getNextNodePos(){
        return this.transform.localPosition.z - this._showLength/2;
    }


    	/**
	 * 设置子材质属性
	 * @param obj 

	 */
	setSubMaterialAttr(obj){
        if(!obj){
            return;
        }
		if(obj instanceof Laya.MeshSprite3D){
			let mat = obj.meshRenderer.material  as Laya.UnlitMaterial;
			mat.tilingOffsetX = this._showLength / this._defaultLength;
            // let text =  mat.albedoTexture as any;
            // text.closeMipmap();
		}
		for(let i=0;i < obj.numChildren; ++i){
			this.setSubMaterialAttr(obj.getChildAt(i) as Laya.Sprite3D);
		}
	}
 }