import GameGlobal from "../Global";
import MouseMeshScript from "../script/device/MouseMeshScript";
import UtilTool from "../util/UtilTool";
import EnumType from "./EnumType";
import RoadNode from "./RoadNode";
import ScnenDevice from "./ScnenDevice";
/**
 * 单条渲染隧道
 */
export default class TunnelSprite extends Laya.Sprite3D{
    /**
     * 隧道长度
     */
    public TunnelLength:number = 200;

    /**
     * 隧道位置类型 0为右洞  1为左洞
     */
    private _posType:number =0; 

    /**
     * 隧道位置平移
     */
    public static POS_OFFSET_VALUE:number = 28.6;

    /**
     * 车道数
     */
    public RoadCount:number = 3;

    /**
     * 隧道节点列表
     */
    private _nodeList:Array<RoadNode>= new Array();

    /**
     * 是否为激活状态
     */
    public _bActive:boolean = true;


    /**
     * 隧道的起始桩号，所有设备位置都应该根据这个值做偏移
     */
    private _startMark:number = 0;


    /**
     * 方向显示
     */
    private _disText:string = "方向显示";
    constructor(){
        super();
    }

    get posType(){
        return this._posType;
    }

    set startMark(num:number){
        this._startMark = num;
    }

    get startMark(){
        return this._startMark;
    }


    /**
     * 根据车道获取车道中心位置
     * @param index
     */
    getRoadPosX(index:number){
        let x = 0;
        switch(index){
            case 1:
                x = -3.7;
                if(GameGlobal.tunnelMgr.laneNums == 2){
                    x = -2.7;
                }
                break;
            case 2:
                if(GameGlobal.tunnelMgr.laneNums == 2){
                    x = 2.7;
                }
                break;
            case 3:
                x = 3.8;
                break;
        }
        if(this.posType == 1){
            x = -x;
        }
        return x;
    }

    /**
     * 初始化隧道
     * @param len 
     * @param posType 
     * @param addData 横洞空洞数据
     */
    init(len:number, posType:number, addData:any){
        this.TunnelLength = len;
        this._posType = posType;
        if(!addData){
            addData = [];
        }

        this.name = "TunnelSprite" + this._posType;
        if(posType == 0){
            // 距离显示
            let num =  Math.floor(GameGlobal.tunnelMgr.getMaxLenth() / 50) + 2;
            for(let i =0; i < num; ++i){
                let dis = this.addChild(new ScnenDevice(EnumType.DEVICE_TYPE_TEXT_DIS))  as Laya.Sprite3D;
                dis.transform.localPosition.z = (50 -50 * i) * GameGlobal.SHOW_SCALE;
            }
        }

        if(this._bActive){
            this.loadRoad(addData);
        }else{
            this.loadKongDong();
        }

        this.transform.localPosition.x = posType * -TunnelSprite.POS_OFFSET_VALUE;
        this.transform.localPosition = this.transform.localPosition;
    }
    private loadKongDong(){
        let road2 = new RoadNode(3, null);
        road2.showLength = GameGlobal.tunnelMgr.getMaxLenth() * GameGlobal.SHOW_SCALE + 36*2;
        this.addChild(road2);
        this._nodeList.push(road2);
    }

    private loadRoad(addData:any){
       // 根据总长度和横洞空洞数据，计算出所有节点
       let roadDataList = [];
       for(let i = 0; i < addData.length; ++i){
           let hd = {
               data:addData[i],
               pos:(addData[i].pileNumber - this.startMark) * GameGlobal.SHOW_SCALE,
           }
           hd.data.leftRightFlag = parseInt(hd.data.leftRightFlag);
           if(hd.data.leftRightFlag == this._posType + 1 || hd.data.leftRightFlag == 3 || hd.data.emptyType != 0){
               if(hd.data.emptyType < 3){
                   roadDataList.push(hd);
               }else if(this._posType == 0){ // 右侧才加机房
                   // 加入机房
                   let li = this.addChild(new ScnenDevice(EnumType.DEVICE_TYPE_JIFANG1)) as ScnenDevice;
                   li.changeData(hd.data);
                   li.transform.localPosition.z = -(hd.data.pileNumber - this.startMark) * GameGlobal.SHOW_SCALE;
               }

           }
       }
       roadDataList.sort(function(a, b){
           return a.pos - b.pos;
       })

       let kouzi = new RoadNode(1);
       this.addChild(kouzi);
       this._nodeList.push(kouzi);

       let lastRoad = kouzi;
       let curlen = 0;
       for(let i = 0; i < roadDataList.length; ++i){
           if(curlen < roadDataList[i].pos){ // 缺一段插入一段
               let road1 = new RoadNode(0, lastRoad);
               road1.showLength = roadDataList[i].pos - curlen;
               this.addChild(road1);
               this._nodeList.push(road1);
               lastRoad = road1;
               curlen = roadDataList[i].pos;
           }
           // 插入空洞横洞
           let ntype = 0;
           let showlen = 5;
           let hengdong = 0;
           switch(roadDataList[i].data.emptyType){
               case 0:// 空洞
                   ntype = 3;
                   showlen = roadDataList[i].data.length * GameGlobal.SHOW_SCALE;
               break;
               case 1: // 人行横洞
                   ntype = this.posType == 0 ? 4:5;
                   hengdong = EnumType.DEVICE_TYPE_HENGDONG1;
                   break;
               case 2: // 车行横洞
                   ntype = this.posType == 0 ? 6:7;
                   hengdong = EnumType.DEVICE_TYPE_HENGDONG2;
                   break;
           }

           // 添加横洞
           if(this.posType == 0 && hengdong != 0){
               let hd = this.addChild(new ScnenDevice(hengdong))  as ScnenDevice;
               hd.changeData(roadDataList[i].data);
               hd.transform.localPosition.z = lastRoad.getNextNodePos() - showlen / 2;
           }

           // 添加带门的节点
           let road2 = new RoadNode(ntype, lastRoad);
           road2.showLength = showlen;
           road2.addData = roadDataList[i].data;
           this.addChild(road2);
           this._nodeList.push(road2);
           lastRoad = road2;
           curlen += road2.showLength;
       }

       if(curlen < this.TunnelLength){ // 剩下的用隧道缩放补上
           let road3 = new RoadNode(0, lastRoad);
           road3.showLength = this.TunnelLength - curlen;
           this.addChild(road3);
           this._nodeList.push(road3);
           lastRoad = road3;
       }

       // 尾部
       let weiba = new RoadNode(2, lastRoad);
       this.addChild(weiba);
       this._nodeList.push(weiba);
    }
    /**
     * 添加设备到隧道
     * @param data
     */
    addDevice(data:any):ScnenDevice{
        if(!this._bActive){
            return;
        }
        let pos = data.pileNumber - this.startMark;
        if(pos > 0 && pos > -this.TunnelLength){
            pos *= GameGlobal.SHOW_SCALE;
        }
        
        let deviceType = 0;
        switch(data.deviceTypeCode){
            case "signallamp": // 信号灯
                deviceType = EnumType.DEVICE_TYPE_SIGNAL_LAMP;
                break;
            case "laneIndicator": // 车道指示器
                deviceType = EnumType.DEVICE_TYPE_LAMP_INDICATOR;
                break;
            case "urgentphone": // 紧急电话
                deviceType = EnumType.DEVICE_TYPE_PHONE;
                break;
            case "controller": // 控制器
                break;
            case "broadcast": // 广播
                deviceType = EnumType.DEVICE_TYPE_BROADCAST;
                break;
            case "conflagration": // 火灾消防
                deviceType = EnumType.DEVICE_TYPE_ALERTOR;
            break;
                case "guidelight": // 诱导灯
                break;
            case "lighting": // 照明灯
                try {
                    if(data.deviceConfig){
                        let subdata = JSON.parse(data.deviceConfig)
                        switch(subdata.deviceSubtypeCode){
                            case "R":
                                deviceType = EnumType.DEVICE_TYPE_LIGHT_ROAD;
                                break;
                            case "T":
                                deviceType = EnumType.DEVICE_TYPE_LIGHT01;
                                break;
                        }
                    }else{
                        deviceType = EnumType.DEVICE_TYPE_LIGHT01;
                    }

                } catch (error) {
                    deviceType = EnumType.DEVICE_TYPE_LIGHT01;
                    //console.log("add device intelligenceboard error:", error);
                }
                break;
            case "lightingloop": // 灯回路 联排
                deviceType = EnumType.DEVICE_TYPE_LIGHT_GRUOP2;
                break;
            case "waterlevel": // 水位检测
                break;
            case "electronicfence": // 电子围栏
                break;
            case "environment": // 机房环境
                break;
            case "tunneldoor": // 横洞卷闸门
                deviceType = EnumType.DEVICE_TYPE_DOOR;
                break;
            case "draughtfan": // 风机
                try {
                    let subdata = JSON.parse(data.deviceConfig)
                    switch(subdata.deviceSubtypeCode){
                        case "Z":
                            deviceType = EnumType.DEVICE_TYPE_DRAUGHT;
                            break;
                        case "S":
                            deviceType = EnumType.DEVICE_TYPE_DRAUGHT2;
                            break;
                    }
                } catch (error) {
                    deviceType = EnumType.DEVICE_TYPE_DRAUGHT2;
                    //console.log("add device intelligenceboard error:", error);
                }
                break;
            case "camera": // 视频监控
            case "eventcamera": // 视频监控
                deviceType = EnumType.DEVICE_TYPE_CAM;
                break;
            case "vi":      // 传感器
            case "winddirection":
            case "co":
            case "windspeed":
            case "temperature":
            case "luminometer":
            case "ammeter":
                deviceType = EnumType.DEVICE_TYPE_SENSOR;
                break;
            case "intelligenceboard": // 情报板
                try {
                    let subdata = JSON.parse(data.deviceConfig)
                    switch(subdata.deviceSubtypeCode){
                        case "M":
                            deviceType = EnumType.DEVICE_TYPE_QB_BOARD;
                            break;
                        case "F":
                            deviceType = EnumType.DEVICE_TYPE_QB_BOARD_F;
                            break;
                    }
                } catch (error) {
                    //console.log("add device intelligenceboard error:", error);
                    deviceType = EnumType.DEVICE_TYPE_QB_BOARD;
                }

                break;
        }
        if(deviceType == 0){
            return null;
        }

        let li = this.addChild(new ScnenDevice(deviceType)) as ScnenDevice;
        li.changeData(data);
        
        li.transform.localPosition.z = -pos;

        return li
    }

    /**
     * 设置方向显示
     * @param text 
     */
    setDirText(text:string){
        if(!this._bActive){
            return;
        }
        this._disText = text + "->->"
        // 方向显示 入口
        let textdir = this.addChild(new ScnenDevice(EnumType.DEVICE_TYPE_TEXT_DIS))  as ScnenDevice;
        textdir.changeShowData(this._disText);
        if(this._posType == 0){
            textdir.transform.localPosition.setValue(14, -1, 16);
        }else{
            textdir.transform.localPosition.setValue(-14, -1, 16);
        }
        textdir.transform.localPosition = textdir.transform.localPosition;

        // 方向显示 出口
        let textdir1 = this.addChild(new ScnenDevice(EnumType.DEVICE_TYPE_TEXT_DIS))  as ScnenDevice;
        textdir1.changeShowData(this._disText);
        if(this._posType == 0){
            textdir1.transform.localPosition.setValue(14, -1, 16 - this.TunnelLength - 40);
        }else{
            textdir1.transform.localPosition.setValue(-14, -1, 16 - this.TunnelLength -40);
        }
        textdir1.transform.localPosition = textdir1.transform.localPosition;


        // 路中方向显示
        let num =  Math.floor(this.TunnelLength / 250) + 1;
        for(let j = 1; j <= this.RoadCount; ++j){
            for(let i =1; i < num; ++i){
                let dis = this.addChild(new ScnenDevice(EnumType.DEVICE_TYPE_TEXT_DIS))  as ScnenDevice;
                dis.changeShowData(this._disText);
                dis.changeData(true);
                dis.transform.localPosition.x = this.getRoadPosX(j);
                dis.transform.localPosition.z = -10 - 250 * i;
                dis.transform.localPosition = dis.transform.localPosition;
            }
        }

    }

    getStartView(){
        let obj = this._nodeList[0].getChildAt(0);
        return obj && obj.getChildByName("view");
    }

    getEndView(){
        let obj = this._nodeList[this._nodeList.length - 1].getChildAt(0);
        return obj && obj.getChildByName("view");
    }
}