import MouseMeshScript from "./MouseMeshScript"

/**
 * 车道指示器
 */
export default class IndicatorScript extends MouseMeshScript{

    private _imgList:Array<string> = new Array("sd_jinzhi02", "sd_zhengxiang01", "sd_jinzhi02", "sd_zuozhuan01");
    private _imgList2:Array<string> = new Array("sd_jinzhi01", "sd_zhengxiang02", "sd_jinzhi011", "sd_zuozhuan02");
    constructor(){
        super();
    }

    onAwake(){
        super.onAwake();
    }

    onStart(){
        super.onStart();

        this._camerOffsetDis = 5;
        this._camerOffsetHeight = 0;
        
        this.nodeSprite.transform.localPosition.y = 7; // 固定高度偏移
        
        this.nodeSprite.transform.localPosition = this.nodeSprite.transform.localPosition;

        let bExit = this.nodeSprite.tunnelSprite.posType == 1;
        if(bExit){
            this.nodeSprite.transform.rotate(new Laya.Vector3(0, 180, 0), true, false);
        }

       this.changeData();
    }


    changeData(){
        let sp = this.owner as Laya.Sprite3D;
        for(let i = 0; i < this._imgList.length; ++i){
            let img = sp.getChildByName(this._imgList[i]);
            if(img){
                img.active = false;
            }
            let img2 = sp.getChildByName(this._imgList2[i]);
            if(img2){
                img2.active = false;
            }
        }
        let showimg = sp.getChildByName(this._imgList[this.nodeSprite.DeviceData.workMode]);
        if(showimg){
            showimg.active = true;
        }

        // 反向通行
        if(this.nodeSprite.DeviceData.workMode == 2){
            showimg = sp.getChildByName(this._imgList2[1]);
            showimg.active = true;
        }else{
            showimg = sp.getChildByName(this._imgList2[0]); // 背面禁止通行
            showimg.active = true;
        }
    }
}