import MouseMeshScript from "./MouseMeshScript";

export default class DoorScript extends MouseMeshScript{
    constructor(){
        super();
    }

    onAwake(){
        // this._defaultX = 10000;
        this._defaultX = 6.8;
        super.onAwake();
    }

    onStart(){

        this._camerOffsetDis = 7;
        this._camerOffsetHeight = 2;

        let meshsp = this.owner as Laya.Sprite3D;
        meshsp.transform.localPosition.x = 4.65;
        if(this.nodeSprite.transform.localPosition.x > 0){
            meshsp.transform.localPosition.x = -0.36;
        }
        meshsp.transform.localPosition = meshsp.transform.localPosition;

        this.nodeSprite.transform.localPosition.y = 2.30; // 固定高度偏移
        //this.nodeSprite.transform.localPosition.x = -6.6;
        
        this.nodeSprite.transform.localPosition = this.nodeSprite.transform.localPosition;
        this.nodeSprite.transform.rotate(new Laya.Vector3(0, -90, 0), true, false);
        this.changeData();
    }

    changeData(){
        let ms = this.owner.getChildByName("sd_juanzhamen01") as Laya.Sprite3D;
        ms.transform.localPositionY = 1.564;
        let z = 1;

        switch(this.nodeSprite.DeviceData.workMode){
            case 0:
                ms.active = true;
                z = 0.6;
                ms.transform.localPositionY = 2.2;
                break;
            case 1:
                z = 1;
                //ms.active = false;
                break;
            case 2:
                z = 0.001;
                //ms.active = true;
                break;
        }
        let ts = ms.transform
        if(ts.localScaleZ != z){
            Laya.Tween.to(ts, {localScaleZ:z,update:new Laya.Handler(this, function(){
                ts.localScale = ts.localScale;
            })}, 2000);
        }

        ms.transform.localPosition = ms.transform.localPosition;
    }
}