import GameGlobal from "../../Global";
import UtilTool from "../../util/UtilTool";
import MouseMeshScript from "./MouseMeshScript";


export default class DisTextScript extends MouseMeshScript{

    private meshsp:Laya.MeshSprite3D;
    constructor(){
        super();
    }


    onStart(){
       this.meshsp = this.owner as Laya.MeshSprite3D;
       this.meshsp.transform.localPosition.y = 0.3;
       let node = (this.meshsp.parent as Laya.Sprite3D);
       if(this.nodeSprite.showData){
           if(!this.nodeSprite.DeviceData){
                this.meshsp.transform.localScale = new Laya.Vector3(1, 1, 0.35); 
           }else{
            node.transform.localScale.setValue(2, 2, 2);
            node.transform.localScale = node.transform.localScale;
           }
           this.setText(this.nodeSprite.showData, "宋体");
       }else{
            let dis = -(this.meshsp.parent as Laya.Sprite3D).transform.localPosition.z;
            dis = this.nodeSprite.tunnelSprite.startMark + Math.ceil(dis / GameGlobal.SHOW_SCALE);
            let text = "K" + Math.floor(dis/1000) + "+" + dis%1000;
            this.setText(text, "宋体");

            this.meshsp.transform.localPosition.y = -0.5;
            node.transform.localPosition.x = -15;
            node.transform.localScale.setValue(2, 2, 2);
            node.transform.localScale = node.transform.localScale;
            node.transform.localPosition = node.transform.localPosition; // 让坐标再生效一次 外面设置不知道为什么不生效
       }
       this.meshsp.transform.localPosition = this.meshsp.transform.localPosition;
       if(this.nodeSprite.tunnelSprite.posType == 1){
            this.meshsp.transform.rotate(new Laya.Vector3(0, 180, 0), true, false);
        }
    }

    /**
     * 设置文本显示
     */
    setText(text:string, font:string){
        let tex = UtilTool.createText3D(text, 50, "rgb(240,240,240)", font);
        let mat = this.meshsp.meshRenderer.material as Laya.EffectMaterial
        mat.texture = tex;
    }
}