/**
 * 传感器
 */
import UtilTool from "../../util/UtilTool";
import MouseMeshScript from "./MouseMeshScript";

export default class SensorScript extends MouseMeshScript{
    constructor(){
        super();
    }

    onAwake(){
        this._defaultX = 6.77;
        super.onAwake();
    }

    onStart(){

        this._camerOffsetDis = -5;
        this._camerOffsetHeight = 0;

        this.nodeSprite.transform.localPosition.y = 7.188; // 固定高度偏移
        this.nodeSprite.transform.localPosition = this.nodeSprite.transform.localPosition;
        this.nodeSprite.transform.rotate(new Laya.Vector3(91, 0, 0), true, false);
        this.changeData();
    }

    changeData(){
        if(this.nodeSprite.DeviceData.contentText){}
       this.showText(this.nodeSprite.DeviceData.contentText, 30);
    }

    /**
    * 设置传感器内容
    * @param str 
    */
   showText(str:string, ftSize:number){
        let meshsp = this.owner.getChildAt(0) as Laya.MeshSprite3D;
        if(!str || str == ""){
            meshsp.active = false;
            return;
        }
        meshsp.active = true;
        let tex = UtilTool.createText3D(str,50, "rgb(250,0,0)", "Microsoft YaHei");
        let mat = meshsp.meshRenderer.material as Laya.EffectMaterial
        mat.texture = tex;
        let textary = str.split(/[\n]/);
        if(textary.length == 1){
            meshsp.transform.localScaleZ = 0.0705931 / 2;
        }else{
            meshsp.transform.localScaleZ = 0.0705931;
        }
        meshsp.transform.localScale = meshsp.transform.localScale;
    }
}