import { BlurEffect } from "./shader/BlurEffect";
import GameConfig from "./GameConfig";
import GameGlobal from "./Global";
import ModelViewer from "./script/ModelViewer";
import GameUI from "./script/GameUI";
import ScnenDevice from "./logic/ScnenDevice";
import MouseMeshScript from "./script/device/MouseMeshScript";
class Main {
	constructor() {
		//根据IDE设置初始化引擎	
		let config = new Config3D();
		config.isAntialias = false;
		// config.octreeCulling = true;
		// config.debugFrustumCulling = true;
		if (window["Laya3D"]) Laya3D.init(Laya.Browser.width, Laya.Browser.height, config);
		else Laya.init(GameConfig.width, GameConfig.height, Laya["WebGL"]);
		Laya["Physics"] && Laya["Physics"].enable();
		Laya["DebugPanel"] && Laya["DebugPanel"].enable();
		Laya.stage.scaleMode = GameConfig.scaleMode;
		Laya.stage.screenMode = GameConfig.screenMode;
		Laya.stage.alignV = GameConfig.alignV;
		Laya.stage.alignH = GameConfig.alignH;
		//兼容微信不支持加载scene后缀场景
		Laya.URL.exportSceneToJson = GameConfig.exportSceneToJson;

		//打开调试面板（通过IDE设置调试模式，或者url地址增加debug=true参数，均可打开调试面板）
		if (GameConfig.debug || Laya.Utils.getQueryString("debug") == "true") Laya.enableDebugPanel();
		if (GameConfig.physicsDebug && Laya["PhysicsDebugDraw"]) Laya["PhysicsDebugDraw"].enable();
		if (GameConfig.stat) Laya.Stat.show();
		Laya.alertGlobalError(true);

		//激活资源版本控制，version.json由IDE发布功能自动生成，如果没有也不影响后续流程
		Laya.ResourceVersion.enable(Laya.Browser.window._VERSION_JSON || "version.json", Laya.Handler.create(this, this.onVersionLoaded), Laya.ResourceVersion.FILENAME_VERSION);
	}

	onVersionLoaded(): void {
		//激活大小图映射，加载小图的时候，如果发现小图在大图合集里面，则优先加载大图合集，而不是小图
		Laya.AtlasInfoManager.enable("fileconfig.json", Laya.Handler.create(this, this.onConfigLoaded));
	}

	onConfigLoaded(): void {
		Laya.Scene3D.load("res/LayaScene_suidao/Conventional/suidao.ls", Laya.Handler.create(this, this.onLoadScene));
	}

	// 下载场景结束
	onLoadScene(scn:Laya.Scene3D){
		Laya.stage.addChild(scn);

		GameGlobal.GameScene = scn;
		GameGlobal.GameCamera = scn.getChildByName("Main Camera") as Laya.Camera;
		GameGlobal.GameCamera.addComponent(ModelViewer);
		GameGlobal.GameCamera.farPlane = 1000; //设置渲染范围
		GameGlobal.GameCamera.fieldOfView = 40;
		GameGlobal.tunnelMgr.init(scn);
		//GameGlobal.GameCamera.clearColor = new Laya.Vector4(0, 0, 0, 0);

		Laya.Browser.window.focus();
		// 定义消息格式
		Laya.Browser.window.addEventListener("message", function(event){
			let msg = event.data;
			switch(msg.msgType){
				case "loadTunnel":
					if(!GameGlobal.tunnelMgr.InitData){
						GameGlobal.tunnelMgr.loadTunnel(msg.msgData, msg.addData);
						GameConfig.startScene && Laya.Scene.open(GameConfig.startScene); //加载IDE指定的场景
					}
					break;
				case "addDevice":
					GameGlobal.tunnelMgr.addDevice(msg.msgData);
					break;
				case "addDevices":
					GameGlobal.tunnelMgr.addDevices(msg.msgData);
				break;
				case "setAutoView": // 是否自动巡游
					GameGlobal.CameraScript.autoView(msg.msgData);
					break;
				case "resetView": // 默认视角
					GameGlobal.CameraScript.reset();
					break;
				case "focus": // 焦点设置
					Laya.Browser.window.focus();
					break;
				case "selectDevice":// 选中指定设备
					let obj = GameGlobal.tunnelMgr.findDevice(msg.msgData) as ScnenDevice;
					if(obj){
						let scr = obj.getChildAt(0).getComponent(MouseMeshScript) as MouseMeshScript;
						scr.cameraMoveToLook(false);
					}
					break;
			}
		});
		if(Laya.Browser.window.parent){
			let msg = {
				msgType:"loaded",
			}
			Laya.Browser.window.parent.postMessage(msg);
		}
		scn.enableLight = false;
		//雾化代码
		// scn.enableFog = true;
		// //设置雾化的颜色
		// scn.fogColor = new Laya.Vector3(0.3,0.3,0.3);
		// //设置雾化的起始位置，相对于相机的距离
		// scn.fogStart = 100;
		// //设置雾化最浓处的距离。
		// scn.fogRange = 1000;
	}

}
//激活启动类
new Main();
